import { SET_SOUND_COMPLETE, REMOVE_SOUND_COMPLETE} from '../types/settings'

const LOCAL_STORAGE_SOUND_COMPLETE = 'soundComplete';
const initState = false
export default (state = initState, action) => {
  switch(action.type) {
    case SET_SOUND_COMPLETE:
      localStorage.setItem(LOCAL_STORAGE_SOUND_COMPLETE, JSON.stringify(true))
      return true

    case REMOVE_SOUND_COMPLETE:
      localStorage.removeItem(LOCAL_STORAGE_SOUND_COMPLETE)
      return false

    default:
      const soundComplete = JSON.parse(localStorage.getItem(LOCAL_STORAGE_SOUND_COMPLETE))
      return soundComplete ? soundComplete : state
  }
}
