import React, { Component } from 'react'
import checkpointPin from '../assets/images/checkpoint-active.png'

const imgStyle = {
  position: 'absolute',
  transitionTimingFunction: 'ease',
  transitionDuration: '1s',
  left: '50%',
  top:  '50%',
  overflow: 'hidden',
}

class CheckinAnimation extends Component {
  state = {
    size: 0,
    opacity: 1,
  }

  componentDidMount() {
    // NOTE: This timer is needed. Because in some twisted situation, render timing break tweening.
    this.timerHandler = setTimeout(() => {this.setState({ size: 3000, opacity: 0 })}, 1)
  }

  componentWillUnmount() {
    clearTimeout(this.timerHandle)
  }

  static getDerivedStateFromProps(props, state) {
    if (state.size !== 0) {
      if (props.callback) {
        props.callback()
      }
    }
    return null
  }

  render() {
    const { size, opacity } = this.state
    const dinamicImgStyle = {
      ...imgStyle,
      width: size,
      height: size,
      marginLeft: -size / 2,
      marginTop: -size / 2,
      opacity,
    }

    // NOTE: height: 100vh sometimes doesn't work properly. For now, `${window.innerHeight)px` is used as alternative
    return (
      <div className="fullScreenFrame forceOverlay" style={{height: `${window.innerHeight}px`}} >
        <img src={checkpointPin} alt='modal animation' style={dinamicImgStyle} />
      </div>
    )
  }
}
export default CheckinAnimation
