import React, { Component } from 'react'
import ModalTemplate from './modal-template'
import InformationIcon from '@material-ui/icons/InfoOutlined';

class SoundInfoModal extends Component {
  state = {
    showModal: false,
  }

  titleStyle = {
    marginTop: `${(window.innerHeight - 118) * .25}px`,
  }

  contentStyle = {
    marginTop: `${(window.innerHeight - 118) * .5}px`,
  }

  shouldComponentUpdate(nextProps,nextState){
    if(this.state.showModal !== nextState.showModal){
      return true;
    }
    return false;
  }

  handleOpenModal = () => {
    if(this.props.soundInfo.unlocked){
      this.setState({ showModal: true });
    }
  }

  handleModal = (showModal) => {
    this.setState({ showModal })
  }

  formatContent = content => {
    // if(!this.props.soundInfo){
    //   return null
    // }
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    }
    )
  }

  render () {
    const { soundInfo } = this.props;

    return (
      <div>
        <InformationIcon style={this.props.style} onClick={this.handleOpenModal} />
        <ModalTemplate showModal={this.state.showModal}
          callBackState={this.handleModal}
          shouldCloseOnOverlayClick={true}
          isShort={false}
          isSmall={this.state.isSmall}
          revealModal
          >
          <center>
            <div className='soundInfoTitle' style={this.titleStyle}>
              {this.formatContent(soundInfo.name)}
            </div>
            <div className='soundInfoContent' style={this.contentStyle}>
              {this.formatContent(soundInfo.comment)}
            </div>
          </center>
        </ModalTemplate>
      </div>
    )
  }
}
export default SoundInfoModal

