import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'; // ref: https://tylermcginnis.com/react-router-programmatically-navigate/
import { setSensorOn } from '../actions/sensor-datas';
// images
import gpsPopupImg from '../assets/images/gps-popup-screen.png';
import pinCurrentImg from '../assets/images/pin-current.png';
import checkpointSampleImg from '../assets/images/checkpoint-sample.png';
import soundListSampleImg from '../assets/images/sound-list-sample.png';
import kirariImg from '../assets/images/kirari.png';
import startTextImg from '../assets/images/text-start.png';
// styles
import '../assets/styles/tutorial-contents.css';

class TutorialContents extends Component {
  imgMarginStyle = {
    marginTop: '4vw'
  }

  renderContent(slideNumber) {
    switch (slideNumber) {
      default:
        return (
          <div></div>
        )

      case 0:
        return (
          <div style={{ textAlign: 'center' }}>
            <div className='jpn'>
              本企画はスマートフォンの位置情報を
              <br />
              活用します。
              <br />
              チュートリアル終了後、
              <br />
              位置情報の利用を許可してください。
            </div>
            <div className='eng'>
              Please allow GPS access.
              </div>
            <center style={this.imgMarginStyle}>
              <img src={gpsPopupImg} alt='gps' width='46%' style={{ pointerEvents: 'none' }}/>
            </center>
          </div>
        )

      case 1:
        return (
          <div style={{ textAlign: 'center' }}>
            <div className='jpn'>
              特定の場所に行くことで音楽が解放される。
              <br />
              そんな新しい体験と
              <br />
              SEKAI NO OWARIがコラボレート！
              <br />
              最新アルバム収録曲を特別先行試聴できます。
              <br />
              ここでしか味わえない特別な音楽を体験しよう!
            </div>
            <div className='eng'>
              Going to a specific place will release special music.
              <br />
              "End of the World" collaborate with songs
              <br />
              from their newest recording album.
              <br />
              Let's enjoy special music in a new experience!
            </div>
          </div>
        )

      case 2:
        return (
          <div style={{ textAlign: 'center' }}>
            <div className='jpn'>
              地図上にある
              <br />
              楽曲タイトルのスポットへ
              <br />
              移動してください。
            </div>
            <div className='eng'>
              Go to a spot to start your journey.
              <br />
              Spot locations are shown on the map page.
            </div>
            <div className='row jpn' style={this.imgMarginStyle}>
              <div className='column'>
                <img className='pinCurrentSample' src={pinCurrentImg} alt='pin' width='100%'/>
                現在地
                </div>
              <div className='column'>
                <img className='checkpointSample' src={checkpointSampleImg} alt='checkpoint' width='100%' />
                スポット
                </div>
            </div>
          </div>
        )

      case 3:
        return (
          <div style={{ textAlign: 'center' }}>
            <div className='jpn'>
              スポットに行くことで、
              <br />
              楽曲が聴けるようになります。
            </div>
            <div className='eng'>
              When you arrive at a spot,
              <br />
              a song will be unlocked and become playable.
            </div>
            <center style={this.imgMarginStyle}>
              <img src={soundListSampleImg} alt='soundlist' width='80%' style={{ pointerEvents: 'none' }} />
            </center>
          </div>
        )

      case 4:
        return (
          <div style={{ textAlign: 'center' }}>
            <div className='jpn'>
              さらに、
              <br />
              ５箇所のスポットをすべて訪れると、
              <br />
              特別先行配信試聴曲と
              <br />
              SEKAI NO OWARI Sound RPG
              <br />
              オリジナルグッズ<sup>※</sup>をプレゼント！
              <br />
              <div className='jpn-small'>
                ※オリジナルグッズは先着での配布となります
              </div>
            </div>
            <div className='eng'>
              After visiting all 5 spots, you can unlock
              <br />
              a special pre-release song
              <br />
              and get an original gift<sup>※</sup>.
              <br />
              <div className='eng-small'>
                ※Original gift will be distributed in first come first served basis.
              </div>
            </div>
            <center style={this.imgMarginStyle}>
              <div>
                <img src={kirariImg} alt='kirari' width='80%' style={{ pointerEvents: 'none', marginTop: '-10vw' }} />
              </div>
            </center>
          </div>
        )

      case 5:
        return (
          <div style={{ textAlign: 'center'}}>
            <div className='jpn'>
              さあ、新しい音楽と
              <br />
              出会いにいきましょう！
            </div>
            <div className='eng'>
              Let's start our journey!
            </div>
          </div>
        )

      case 6:
        return(
          <center style={this.imgMarginStyle}>
            <div className='startButtonGlow' onClick={this.onButtonClick}>
              <img src={startTextImg} alt='startText' width='55%' style={{ paddingTop: '100%', transform: 'translateY(-50%)', pointerEvents: 'none' }} />
            </div>
          </center>
        )
    }
  }

  onButtonClick = () => {
    this.props.setSensorOn();
    this.props.history.push('/map');
  }

  render() {

    return (
      <div>
        {this.renderContent(this.props.slideNumber)}
      </div>
    )
  }
}

export default connect(state => state, { setSensorOn })(withRouter(TutorialContents))
