import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { imgStyle } from './style-check-points.js';
import pinNote from '../../assets/images/checkpoint-active.png';
import pinDone from '../../assets/images/checkpoint-inactive.png';
// styles
import '../../assets/styles/marker-check-points.css';

const STATIC_IMG_SIZE = 120;

class MarkerCheckPoints extends Component {
  staticImgStyle = {
    ...imgStyle,
    width: STATIC_IMG_SIZE,
    height: STATIC_IMG_SIZE,
    left: -STATIC_IMG_SIZE / 2,
    top: -STATIC_IMG_SIZE / 2,
  }

  render() {
    // Fix the size if users are far from the area
    let pointSize = 120
    if (this.props.zoom > 14) {
      // Otherwise, set proper size of icon
      pointSize = Math.pow(2, this.props.zoom) / 1000
    }
    const dinamicImgStyle = {
      ...imgStyle,
      width: pointSize,
      height: pointSize,
      left: -pointSize / 2,
      top: -pointSize / 2,
    }

    const pin = this.props.unlocked ? pinDone : pinNote;
    const title = `assets/images/checkpoint-title-${this.props.soundId}.png`;

    return (
      <div>
        <img className='checkpoint-active' src={pin} alt='checkpoints ring' style={dinamicImgStyle} />
        {/* <img className={'checkpoint-active-' + this.props.tag} src={pin} alt='checkpoints ring' style={dinamicImgStyle} /> */}
        {this.props.unlocked ? null : <img src={title} alt='checkpoints title' style={this.staticImgStyle} />}
        {/* <img src={title} alt='checkpoints title' style={this.staticImgStyle} /> */}
      </div>
    )
  }
}

export default withRouter(MarkerCheckPoints)
