import React, { Component } from 'react';
import ModalTemplate from './modal-template';
// styles
import '../assets/styles/complete-song-modal.css';

class CompleteSongModal extends Component {
  titleStyle = {
    marginTop: `${(window.innerHeight - 118) * .25}px`,
  }

  contentStyle = {
    marginTop: `${(window.innerHeight - 118) * .5}px`,
  }

  onModalClosed = () => {
    if (this.props.onModalClosed) {
      this.props.onModalClosed()
    }
  }

  formatContent = content => {
    // if(!this.props.soundInfo){
    //   return null
    // }
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    }
    )
  }

  render () {
    const { soundInfo } = this.props

    return (
      <ModalTemplate defaultShowModal={true}
                    shine
                    callbackClose={this.onModalClosed} >
          <div className='modal-title' style={{fontSize: '5vw'}}>
            シークレットトラック入手!
          </div>
          <div className='soundInfoTitle' align='center' style={this.titleStyle}>
            {this.formatContent(soundInfo.name)}
          </div>
          <div className='soundInfoContent' align='center' style={this.contentStyle}>
            {this.formatContent(soundInfo.comment)}
          </div>
      </ModalTemplate>
    )
  }
}
export default CompleteSongModal
