import React, { Component } from 'react';
import $ from 'jquery';
// components
import ModalTemplate from '../components/modal-template';
//import MapIcon from "@material-ui/icons/MapTwoTone";
// images
import MapIcon from '../assets/images/mapIcon.svg';
import map from '../assets/images/map-songs.png';
import IconOne from '../assets/images/number-one.svg';
import IconTwo from '../assets/images/number-two.svg';
import IconThree from '../assets/images/number-three.svg';
import IconFour from '../assets/images/number-four.svg';
import IconFive from '../assets/images/number-five.svg';

import _ from 'lodash';

class EntireMapModal extends Component {
  state = {
    showModal: false
  }

  items = {
    '01': {num: 1,icon: IconOne,title: 'さっぽろ雪まつり大通り会場 ５丁目',bodyJP:'曲：ANTI-HERO',bodyEN:'Sapporo snow festival, 5-chome \nMusic: ANTI-HERO'},
    '02': {num: 2,icon: IconTwo,title: 'さっぽろ雪まつり大通り会場 ２丁目',bodyJP:'曲：SOS',bodyEN:'Sapporo snow festival, 2-chome \nMusic: SOS'},
    '03': {num: 3,icon: IconThree,title: '札幌PARCO',bodyJP:'曲：サザンカ',bodyEN:'PARCO Sapporo \nMusic: Sazanka'},
    '04': {num: 4,icon: IconFour,title: 'ソニーストア札幌',bodyJP:'曲：イルミネーション',bodyEN:'Sony store Sapporo \nMusic: Illumination'},
    '05': {num: 5,icon: IconFive,title: 'セイコーマート南１条西４丁目店',bodyJP:'曲：Silent Night',bodyEN:'Seico mart \nMusic: Silent Night'},
  }

  handleOpenModal = () => {
    this.setState({ showModal: true });
  }

  handleModal = (showModal) => {
    this.setState({ showModal })
  }

  renderList () {
    let arr = _.map(this.items, (item, key)=>{
      return(
        <tbody key={key}>
        <tr>
            <td><div><img src={item.icon} alt="icon" width="17" height="17" align="top" style={{ pointerEvents: 'none' }}  /></div></td>
          <td> <div className='modal-content-listTitle'>{item.title} </div></td>
        </tr>
        <tr>
          <td></td>
          <td> <div className='modal-content-jp'>{item.bodyJP} </div></td>
        </tr>
        <tr>
          <td></td>
          <td> <div className='modal-content-en'>{this.formatContent(item.bodyEN)} </div></td>
        </tr>
        <tr>
          <td>&nbsp;</td>
          <td></td>
        </tr>
        </tbody>
      )
    });
    return _.sortBy(arr, ['key'])
  }

  formatContent = (content) => {
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    })
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }

    return (
      <div>
        {/*<MapIcon onClick={this.handleOpenModal} style={this.props.style}/>*/}
        <img src={MapIcon} alt="" width="40" height="40" onClick={this.handleOpenModal}/>
        <ModalTemplate
          showModal={this.state.showModal}
          revealModal
          callBackState={this.handleModal}>
        <div className="modal-miniTitle" onClick={this.scrollTop}>
            全体マップ<br />
            Area map
          </div>
          <div className={`modal-content scroll-modal`} align='center' style={style}>
            <div>
              <img src={map} alt="Map" width='100%' style={{ pointerEvents: 'none' }} />
            </div>
            <table width='100%'>
              {this.renderList()}
            </table>
          </div>
        </ModalTemplate>
      </div>
    )
  }
}
export default EntireMapModal

