import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeSoundLock, changeCouponVisible, setSoundComplete } from '../actions/settings';
import Button from '@material-ui/core/Button'

class ManualCheckin extends Component{

  onCheckIn = (area) => {
    const { soundId, couponId } = area
    this.props.changeSoundLock(soundId);
    if (couponId) {
      this.props.changeCouponVisible(couponId);
    }
  }

  onComplete = () => {
    this.props.setSoundComplete()
  }

  renderList = () => {
    return _.map(this.props.settingData.appSetting.areas, (area, key) => {
      return (
        <p key={key}>
          <Button variant='contained' onClick={() => {this.onCheckIn(area)}}>
            {area.soundId} { area.couponId && `(Coupon: ${area.couponId})` }
          </Button>
        </p>
      )
    })
  }
  render(){
    return(
      <div>
        { this.renderList() }
        <p>
          <Button variant='contained' onClick={this.onComplete}>
            Complete
          </Button>
        </p>
      </div>
    )
  }
}

export default connect(state => state, { changeSoundLock, changeCouponVisible, setSoundComplete })(ManualCheckin)
