import { SET_CURRENT_AREA, RESET_CURRENT_AREA } from '../types/statuses'

const initState = false
export default (state = initState, action) => {
  switch(action.type) {
    case SET_CURRENT_AREA:
      return action.payload
    case RESET_CURRENT_AREA:
      return initState
    default:
      return state
  }
}

