import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import { sha256 } from 'js-sha256';

class PresentCheckout extends Component{
  state = {
    password : 'Password',
    formHelpText: 'パスワードを入力してください',
    requested : false
  }

  componentDidMount(){
    // Password is '1234'
    this.CURRECT_PASS = '03ac674216f3e15c761ee1a5e255f067953623c8b388b4459e13f978d7c846f4';
    this.LOCALSTARAGE_KEY = 'PresentCheckout'
    if(localStorage.getItem(this.LOCALSTARAGE_KEY)){
      this.setState({
        requested: true,
        formHelpText: '応募済みです'
      });
    }
  }

  handleChange = (event) => {
      this.setState({ password: event.target.value });
  };

  applyPassword = () => {
    if(sha256(this.state.password) === this.CURRECT_PASS){
      this.setState({
        requested: true,
        formHelpText: '応募済みです'
      });
      localStorage.setItem(this.LOCALSTARAGE_KEY, true);
    } else {
      this.setState({formHelpText: 'パスワードが違います'});
    }
  }

  resetCheckout = () => {
    localStorage.removeItem(this.LOCALSTARAGE_KEY);
    this.setState({
      password : 'Password',
      formHelpText: 'パスワードを入力してください',
      requested: false
    })
  }

  render(){
    return(
    <form>
      {/* username input is not needed, but only for removing chrome's warning */}
      <input type="text" name="username" style={{display:"none"}} autoComplete="username"/>
      <TextField
          disabled={this.state.requested}
          id="standard-password-input"
          label="Password"
          type="password"
          autoComplete="current-password"
          margin="normal"
          onChange={this.handleChange}
          helperText={this.state.formHelpText}
        />
      <Button variant='contained'  onClick={this.applyPassword} disabled={this.state.requested}>
        応募する
      </Button>
      <Button variant='contained'  onClick={this.resetCheckout} >
        Reset
      </Button>
    </form>
    )
  }
} export default connect( state => state, {})(PresentCheckout)
