import React, { Component } from 'react';
// import _ from 'lodash';
import $ from 'jquery';
// components
import ModalTemplate from '../components/modal-template';
// images
import Title from '../assets/images/app-title.png';
import Credits from '../assets/images/sponsors-logo.png';
// import { ListItemIcon } from '@material-ui/core';

class InformationModal extends Component {
  constructor(props){
    super(props);

    this.serviceTerms=[
      {title: '',body:'この利用規約（以下，「本規約」といいます。）は，株式会社北海道新聞社（以下，「当社」といいます。）がこのウェブサイト上で提供するサービス（以下，「本サービス」といいます。）の利用条件を定めるものです。ユーザーの皆さま（以下，「ユーザー」といいます。）には，本規約に従って，本サービスをご利用いただきます。'},
      {title: '第1条（適用)',body:'本規約は，ユーザーと当社との間の本サービスの利用に関わる一切の関係に適用されるものとします。'},
      {title: '第2条（利用について）',body:'本コンテンツは、実際の街中で実施いたします。一般の方や近隣住民の方への迷惑行為、本イベントとは関係のない私有地への侵入はおやめください。交通ルールを遵守し、道路上への飛び出しや歩きスマホはおやめください。また、車や自転車を運転中の「ながらスマホ」は危険ですのでおやめください。'},
      {title: '第3条（禁止事項）',body:'ユーザーは，本サービスの利用にあたり，以下の行為をしてはなりません。\n\n(1)法令または公序良俗に違反する行為\n\n(2)犯罪行為に関連する行為\n\n(3)当社のサーバーまたはネットワークの機能を破壊したり，妨害したりする行為\n\n(4)当社のサービスの運営を妨害するおそれのある行為\n\n(5)他のユーザーに関する個人情報等を収集または蓄積する行為\n\n(6)他のユーザーに成りすます行為\n\n(7)当社のサービスに関連して，反社会的勢力に対して直接または間接に利益を供与する行為\n\n(8)その他，当社が不適切と判断する行為'},
      {title: '第4条（本サービスの提供の停止等）',body:'当社は，以下のいずれかの事由があると判断した場合，ユーザーに事前に通知することなく本サービスの全部または一部の提供を停止または中断することができるものとします。\n\n(1)本サービスにかかるコンピュータシステムの保守点検または更新を行う場合\n\n(2)地震，落雷，火災，停電または天災などの不可抗力により，本サービスの提供が困難となった場合\n\n(3)コンピュータまたは通信回線等が事故により停止した場合\n\n(4)その他，当社が本サービスの提供が困難と判断した場合\n\n当社は，本サービスの提供の停止または中断により，ユーザーまたは第三者が被ったいかなる不利益または損害について，理由を問わず一切の責任を負わないものとします。'},
      {title: '第5条（免責事項）',body:'本コンテンツはユーザーサービスであり、利用中に生じる責任はユーザーに帰属いたします。利用によって生じるいかなる問題に関しましても、当サービスおよび、当社はいかなる責任、義務も負いません。当社は，本サービスに関して，ユーザーと他のユーザーまたは第三者との間において生じた取引，連絡または紛争等について一切責任を負いません'},
      {title: '第6条（サービス内容の変更等）',body:'当社は，ユーザーに通知することなく，本サービスの内容を変更しまたは本サービスの提供を中止することができるものとし，これによってユーザーに生じた損害について一切の責任を負いません。'},
      {title: '第7条（利用規約の変更）',body:'当社は，必要と判断した場合には，ユーザーに通知することなくいつでも本規約を変更することができるものとします。'},
      {title: '第8条（権利義務の譲渡の禁止）',body:'ユーザーは，当社の書面による事前の承諾なく，利用契約上の地位または本規約に基づく権利もしくは義務を第三者に譲渡し，または担保に供することはできません。'},
      {title: '第9条（各種データに関して）',body:'本コンテンツは、ユーザーのご利用いただくモバイル端末の位置情報と姿勢情報をもとにサービスを受けられるよう設計されております。利用範囲と扱いについては以下になります。尚、当サイトがリンクする他のウェブサイトには当規約は適用されません。これらのウェブサイトにおける個人情報の取り扱いについては、当該ウェブサイトの個人情報保護方針などをご参照ください。\n\n位置情報を外部からの読み取りができないセキュアな通信（SSL通信）でサーバーに送信します。位置情報については、サーバーで一時的な判定で使用しており、例えば、現在の位置から楽曲が試聴できる範囲かどうかを判定する際や，ユーザが識別されない形式での位置情報統計データ活用に利用しています。それ以外で、ユーザーのご利用いただくモバイル端末から情報を取得することはなく、その情報を第3者に開示することもありません。\n\nコンテンツでは以下の目的によりブラウザのローカルストレージ（Local Strage）を利用しています。\n\n・ユーザーがサイトで楽曲を試聴可能になった後、再度サイトにアクセスした際に楽曲ごとの視聴可否に関する情報を維持するため\n\n・ユーザが本コンテンツと連動したプレゼント応募企画へ参加したかどうかの情報を維持するため\n\nユーザーはブラウザの設定によりローカルストリージのアクセスの拒否や除法の削除が可能ですが、その場合には本コンテンツが正常にご利用いただけないなどの制約が生じる可能性があります。'},
      {title: '第10条（音声コンテンツに関して）',body:'本サービスで公開している音声コンテンツを権利者に無断で公開、転載、コピー、改変、ダウンロードすることはできません。'},
      {title: '第11条（準拠法・裁判管轄）',body:'本規約の解釈にあたっては，日本法を準拠法とします。本サービスに関して紛争が生じた場合には，当社の本店所在地を管轄する裁判所を専属的合意管轄とします。'},
    ]

    

    this.informationItems =[
      {title: 'キャンペーン概要',body:'SEKAI NO OWARI ４年振りのニューアルバム「Eye」「Lip」（２０１９年２月２７日発売）に収録される楽曲がさっぽろ雪まつり会場をはじめ市内中心部でいち早く楽しめる！ \nWebブラウザを開き、５箇所のスポットを訪れると、それぞれの場所で異なるアルバム収録曲を試聴することができます。さらに、５箇所のスポットをすべて巡ると、特別先行配信楽曲とSEKAI NO OWARI Sound RPG グッズ※をプレゼント！',remark:'※オリジナルグッズは先着での配布となります。'},
      {title: '実施期間',body:'2019.2.4(Mon) ~ 2019.2.11(Mon)',remark:'実施期間以降は楽曲の再生やクーポンのご利用が出来なくなります。'},
      {title: '推奨ブラウザ・推奨環境',body:'iOS10 以降の Safari、または、Android の Chrome58 以降をご利用ください。',remark:''},
      {title: '注意事項',body:'取得した楽曲は、同じブラウザで試聴してください。別のブラウザでは試聴できません。\n\nプライベートモード・シークレットタブでの利用は、動作を保証できたいため、お控えください。',remark:''},
      {title: '利用規約',body:this.serviceTerms, remark:''},
    ]

  }

  shouldComponentUpdate(nextProps, prevState) {
    if(nextProps.showModal !== this.props.showModal){
      return true
    }
    return false
  }

  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  createInfomationTable(itemsArray){
    let tableItemsJSX = [];
    for(let index in itemsArray){
      let content;

      if(itemsArray[index].title === ''){
        content= (
          <tbody key={index}>
          <tr>
            <td colSpan='2'><div className='modal-content-jp'>{(typeof(itemsArray[index].body) === 'object')? this.renderInfomationTable(itemsArray[index].body) : this.formatContent(itemsArray[index].body)} </div></td>
          </tr>
          <tr>
            <td>&nbsp;</td>
            <td></td>
          </tr>
          </tbody>
        )
      }else{
        content= (
          <tbody key={index}>
          <tr>
            <td><div className="circle-white">&nbsp;</div> </td>
            <td><div className='modal-content-listTitle'>{itemsArray[index].title}</div> </td>
          </tr>
          <tr>
            <td></td>
            <td> <div className='modal-content-jp'>{(typeof(itemsArray[index].body) === 'object')? this.renderInfomationTable(itemsArray[index].body) : this.formatContent(itemsArray[index].body)} </div></td>
          </tr>
          <tr>
            <td></td>
            <td> <div className='modal-content-jp'>{itemsArray[index].remark} </div></td>
          </tr>
          <tr>
            <td>&nbsp;</td>
            <td></td>
          </tr>
          </tbody>
        )
      }

      tableItemsJSX.push(content);
    }
    return tableItemsJSX
  }

  renderInfomationTable(array){
    return(
      <table>
        {this.createInfomationTable(array)}
      </table>
    )
  }

  formatContent = (content) => {
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    }
    )
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }

    return (
      <div>
        {/* {console.log('info')}         */}
        <ModalTemplate showModal={this.props.showModal}
          callBackState={this.handleModal} 
          shouldCloseOnOverlayClick={true}
          isShort={false}
          isSmall={this.props.isSmall}
          revealModal
          >
           <div className="modal-miniTitle" onClick={this.scrollTop}>
            インフォメーション<br />
            Information
          </div>
          <div className={`modal-content scroll-modal`} style={style}>
            <img src={Title} alt="Title" width='100%' style={{ pointerEvents: 'none' }}/>
            {/* <table>
            {this.renderListFirstLevel()}
            </table> */}
            {this.renderInfomationTable(this.informationItems)}
            <img src={Credits} alt="Credits" width='100%' style={{ pointerEvents: 'none' }}/>
          </div>
        </ModalTemplate>
      </div>
    )
  }
}
export default InformationModal

