import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import GoogleMapReact from 'google-map-react';
// redux
import { connect } from 'react-redux';
import { GOOGLE_MAP_KEY } from '../config/worldscale-ar-api'
// component
import MarkerCurrentPin from '../components/google-map-items/marker-current-pin';
import MarkerCheckPoints from '../components/google-map-items/marker-check-points';
import GeoUtility from '../utils/common/geo-utility';
import HamburgerMenu from '../components/hamburger-menu';
import CheckinModals from '../components/checkin-modals'
import OutOfAreaModal from '../components/out-of-area-modal'
import FirstTimeGuideDialog from '../components/first-time-guide-dialog';
import EntireMapModal from '../components/entire-map-modal';
// actions
import { setCurrentArea, setInEventArea } from '../actions/statuses';
import { changeSoundLock, changeCouponVisible} from '../actions/settings';
// import { setGeolocation } from '../actions/sensor-datas';
import { requestSendLog } from '../actions/send-log-api'
import { AREA_IN, COUPON } from '../types/logs'
// styles
import { MapStylesSoundArtelligence } from '../components/google-map-items/map-styles';
import '../assets/styles/map.css';

//import MyLocationIcon from "@material-ui/icons/MyLocationTwoTone";
import MyLocationIcon from '../assets/images/myLocationIcon.svg';

import PreloadImage from '../containers/preload-image';

class MapFrame extends Component {
  areas = this.props.settingData.appSetting.areas

  state = {
    center: this.props.geolocation.pos,
    draggable: true,
    rawPosition: {
      lat: this.props.geolocation.pos.lat,
      lng: this.props.geolocation.pos.lng
    },
    mapPosition: {
      lat: this.props.geolocation.pos.lat,
      lng: this.props.geolocation.pos.lng
    },
    currentArea: false,
    zoom: 17,
    inAreaZoom: 17,
    minZoom: 5,
    maxZoom: 21,
    disableDefaultUI: true,
    backgroundColor: 'black',
    styles: MapStylesSoundArtelligence,
    // showModal: false,
    opacity: '0',
  };

  outAreaZoom = 5;

  componentDidMount() {
    const INTERVAL_OF_PROGRESS_UPDATE = 30; //[ms]
    this.changeOpacity();
    this.intervalHandle = setInterval(this.applyMapPosition, INTERVAL_OF_PROGRESS_UPDATE);
    // Important: Without this if, outAreaZoom won't be applied, if you are initially out of the area
    if (!this.props.inEventArea) {
      this.setState({zoom: this.outAreaZoom});
    }
  }
  componentWillUnmount() {
    clearInterval(this.intervalHandle);
  }

  changeOpacity = () => {
    this.setState({
      opacity: 1
    });
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    let retState = {};
    if (nextProps.currentArea !== prevState.currentArea) {
      retState.currentArea = nextProps.currentArea;
    }
    if (nextProps.settingData.appSetting.useGeolocation &&
        (nextProps.geolocation.pos.lat !== prevState.mapPosition.lat ||
        nextProps.geolocation.pos.lng !== prevState.mapPosition.lng)) {
      retState.rawPosition = { lat: nextProps.geolocation.pos.lat, lng: nextProps.geolocation.pos.lng };
    }
    return retState
  }

  getSnapshotBeforeUpdate(prevProps, prevState) {
    this.applyCurrentArea(this.state.mapPosition);
    this.onAreaChanged(prevState.currentArea, this.state.currentArea);
    this.checkWithinEventArea(this.state.mapPosition);

    if (prevProps.inEventArea !== this.props.inEventArea) {
      if (this.props.inEventArea) {
        this.setState({zoom: this.state.inAreaZoom});
      } else {
        this.setState({inAreaZoom: this.state.zoom});
        this.setState({zoom: this.outAreaZoom});
      }
    }

    return null;
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    //console.log(this.areas);
  }

  applyMapPosition = () => {
    const UPDATE_POSITION_THLESHOLOD = 0.000001;
    const { mapPosition, rawPosition } = this.state;
    const newPosition = {
      lat: rawPosition.lat,
      lng: rawPosition.lng,
    }
    if ((Math.abs(mapPosition.lat - rawPosition.lat) > UPDATE_POSITION_THLESHOLOD) ||
        (Math.abs(mapPosition.lng - rawPosition.lng) > UPDATE_POSITION_THLESHOLOD)) {

      const LERP_ALPHA = 0.2;
      newPosition.lat = LERP_ALPHA * rawPosition.lat + (1 - LERP_ALPHA) * mapPosition.lat
      newPosition.lng = LERP_ALPHA * rawPosition.lng + (1 - LERP_ALPHA) * mapPosition.lng
    }
    this.setState({ mapPosition: newPosition })
  }

  // handleOpenModal = () => {
  //   this.setState({ showModal: true });
  // }

  // handleModal = (showModal) => {
  //   console.log(showModal)
  //   this.setState({ showModal })
  // }

  onAreaChanged(prevArea, currentArea) {
    if (prevArea === currentArea) {
      return
    }
    if (currentArea in this.areas) {
      const {soundId, couponId} = this.areas[currentArea];
      if (soundId) {
        // API Call: Area In
        const params = { area_id: currentArea}
        if (!this.props.settingData.soundList[soundId].unlocked) {
          this.props.requestSendLog(AREA_IN, params)
          this.props.changeSoundLock(soundId);
        }
      }
      if (couponId) {
        // API Call: Coupon acquisition
        if (!this.props.settingData.couponList[couponId].visible) {
          const params = { coupon_id: couponId, action: 'acquire' }
          this.props.requestSendLog(COUPON, params)
        }
        if (!this.props.settingData.couponList[couponId].visible) {
          this.props.changeCouponVisible(couponId);
        }
      }
    }
    this.setState({
      currentArea
    })
    // Do something here when area changes
  }

  applyCurrentArea(position) {
    const geoUtility = new GeoUtility();
    const inArea = geoUtility.nearestArea(position, this.areas)
    if (inArea !== this.props.currentArea) {
      this.props.setCurrentArea(inArea);
    }
  }

  checkWithinEventArea(pos){
    const EVENT_AREA_RADUIS = this.props.settingData.appSetting.eventAreaRadius; //[m]
    const geoUtil = new GeoUtility();
    const originPos = this.props.settingData.appSetting.startPosition;
    const dist = geoUtil.getDist(pos.lat, pos.lng, originPos.lat, originPos.lng);
    let inEventArea = null;
    if(dist >= EVENT_AREA_RADUIS){
      inEventArea = false;
      // return (<OutOfAreaModal />)
    }else{
      inEventArea = true;
    }
    if(inEventArea !== this.props.inEventArea){
      this.props.setInEventArea(inEventArea);
    }
  }

  explicitSetCurrentLocation = (lat, lng) => {
    if (!this.props.settingData.appSetting.useGeolocation) {
      const rawPosition = { lat, lng };
      this.setState({ rawPosition });
      //this.props.setGeolocation(mapPosition)
    }
  }

  onUserMarkerClick = (childKey, childProps, mouse) => {
    if (!this.props.settingData.appSetting.useGeolocation) {
      // function is just a stub to test callbacks
      this.explicitSetCurrentLocation(childKey.lat, childKey.lng);
    }
  }
  // dragging ref: https://jsbin.com/molerasebo/2/edit?js,console,output
  onUserMarkerDrag = (childKey, childProps, mouse) => {
    if (!this.props.settingData.appSetting.useGeolocation) {
      // function is just a stub to test callbacks
      this.setState({ draggable: false });
      this.explicitSetCurrentLocation(mouse.lat, mouse.lng);
    }
  }

  onUserMarkerRelease = (childKey, childProps, mouse) => {
    if (!this.props.settingData.appSetting.useGeolocation) {
      this.setState({ draggable: true });
    }
  }

  _onChange = ({ center, zoom }) => {
    const state = {
      center: center
    };
    // Important: without this if, default zoom for out area will not be functioned
    if (this.props.inEventArea) {
      state.zoom = zoom;
    }
    this.setState(state);
  }

  renderCheckpoints = () => {
    return _.map(this.areas, (checkpoint, key) => {
      return <MarkerCheckPoints
              key={key}
              id={key}
              tag={key}
              unlocked={this.props.settingData.soundList[checkpoint.soundId].unlocked}
              soundId={checkpoint.soundId}
              lat={checkpoint.lat}
              lng={checkpoint.lng}
              zoom={this.state.zoom}
              zIndex={1} />
    })
  }

  onCenteringClick = () => {
    this.setState({center: this.state.mapPosition});
  }

  render() {
    const { compassHeading } = this.props;
    if (this.state.currentArea){
      this.buttonDisplay = {
        visibility: 'visible',
        opacity: '1'
      };
    } else {
      this.buttonDisplay = {
        visibility: 'hidden',
        opacity: '0'
      };
    }

    let style={ fontSize: "40" ,color: "white"};

    return (
      <div className='map'
        style={{
          position: 'fixed',
          height: `${window.innerHeight}px`,
          width: '100vw',
          transition: 'opacity .7s, filter .7s',
          opacity: this.state.opacity,
        }}
      >
        <PreloadImage />
        <HamburgerMenu soundList={this.props.settingData.soundList} />
        <GoogleMapReact
          draggable={this.state.draggable}
          onChange={this._onChange}

          bootstrapURLKeys={{ key: GOOGLE_MAP_KEY }}
          center={this.state.center}
          zoom={this.state.zoom}
          options={{
            minZoom: this.state.minZoom,
            maxZoom: this.state.maxZoom,
            disableDefaultUI: this.state.disableDefaultUI,
            backgroundColor: this.state.backgroundColor,
            styles: this.state.styles,
          }}

          onClick={this.onUserMarkerClick}
          onChildMouseUp={this.onUserMarkerRelease}
          onChildMouseDown={this.onUserMarkerDrag}
          onChildMouseMove={this.onUserMarkerDrag}
        >
          {this.renderCheckpoints()}
          <MarkerCurrentPin
            lat={this.state.mapPosition.lat}
            lng={this.state.mapPosition.lng}
            zIndex={2}
            rotate={compassHeading}
          />
        </GoogleMapReact>

        <div className="centeringButton" onClick={this.onCenteringClick} style={{ bottom: this.props.inEventArea ? '120px' : '15px' }}>
          {/*<MyLocationIcon style={style} />*/}
          <img src={MyLocationIcon} alt="" width="40" height="40"/>
        </div>

        <div className="mapButton" style={{bottom: this.props.inEventArea ? '120px': '15px'}}>
          <EntireMapModal style={style} />
        </div>

        {(this.props.inEventArea)?
         <Fragment>
           <CheckinModals deviceOrientation={this.props.deviceOrientation} />
           <FirstTimeGuideDialog
             inEventArea={this.props.inEventArea}
           />
         </Fragment>
         : <OutOfAreaModal />};
      </div>
    )
  }
}

MapFrame.propTypes = {
  geolocation: PropTypes.object.isRequired,
  compassHeading: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]).isRequired,
  deviceOrientation: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]).isRequired,
};

export default connect(state => state, {
  setCurrentArea,
  changeSoundLock,
  // setGeolocation,
  setInEventArea,
  changeCouponVisible,
  requestSendLog,
})(MapFrame)
