// react
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
// import { TransitionGroup, CSSTransition } from 'react-transition-group'; // for animated transitions
import { LastLocationProvider } from 'react-router-last-location';
import ScrollMemory from 'react-router-scroll-memory';
// component
import MapWrapper from './containers/map-wrapper';

import Main from './containers/main';
// test components
import TestMenu from './test-components/test-menu';
import RemoveAllStorage from './test-components/remove-all-storage';
import Share from './components/share';
import DGLabAdmin from './components/dglab-admin';
// import Thanks from './components/thanks';
// import SensorChecker from './test-components/sensor-checker';
// import CameraChecker from './test-components/camera-checker';
//import SoundChecker from './test-components/sound-checker';
