// import Teaser from './components/teaser';

class AppRoutes extends Component {
  render() {
    const { geolocation, compassHeading, deviceOrientation, isSupported } = this.props
    return (
      <BrowserRouter>
        <LastLocationProvider>
          <div className='main-content'>
            <ScrollMemory />
            <Switch>
              <Route exact path='/'
                     render={(props) => <Main {...props}
                                          isSupported={isSupported}
                                          geolocation={geolocation}
                                          sensorOn={this.props.sensorOn}
                                          endDateTime={this.props.settingData.appSetting.endDateTime}
                 />}
              />
              <Route exact path='/map'
                     render={(props) => <MapWrapper {...props}
                                          geolocation={geolocation}
                                          compassHeading={compassHeading}
                                          deviceOrientation={deviceOrientation}
                     />}
              />
              <Route exact path='/share' component={Share} />

              {/* --------------- testing purposes ------------------ */}
              <Route exact path='/dglab-admin' component={DGLabAdmin} />
              <Route exact path='/refresh' component={RemoveAllStorage} />
              <Route exact path='/menu-test' component={TestMenu} />
              {/* <Route exact path='/thanks' component={Thanks} /> */}
              {/*<Route exact path='/sensor-checker'
                     render={(props) => <SensorChecker {...props}
                                          geolocation={geolocation}
                                          compassHeading={compassHeading}
                                          deviceOrientation={deviceOrientation}
                     />}
              />
              <Route exact path='/log-checker/:logType' component={LogChecker}/>
              <Route exact path='/log-checker' component={LogChecker} />
              <Route exact path='/camera-checker' component={CameraChecker} />
              <Route exact path='/sound-checker/:areaId' component={SoundChecker} />
              <Route exact path='/sound-checker' component={SoundChecker} />
              <Route exact path='/ooa' component={OOA} />
              <Route exact path='/Teaser' component={Teaser} />*/}
            </Switch>
          </div>
        </LastLocationProvider>
      </BrowserRouter>
    )
  }
}

export default connect(state => state, {})(AppRoutes);
