import { STORAGE_DID_READ_TUTORIAL } from '../types/tutorial'
import { SET_SENSOR_ON } from '../types/sensor-datas'

const initState = false
export default (state = initState, action) => {
  switch(action.type) {
    case SET_SENSOR_ON:
      localStorage.setItem(STORAGE_DID_READ_TUTORIAL, action.payload)
      return action.payload
    default:
      const didReadTutorial = localStorage.getItem(STORAGE_DID_READ_TUTORIAL)
      return didReadTutorial ? true: state
  }
}

