import React, { Component } from 'react';
import { connect } from  'react-redux';
import { withRouter } from 'react-router';
import { Redirect } from 'react-router-dom';

import StampRally from '../components/stamp-rally';
import HelpModal from '../components/help-modal';
import TutorialModal from '../components/tutorial-modal';
import CouponModal from '../components/coupon-modal';
import InfoModal from '../components/information-modal';
import QRCodeModal from '../components/qrcode-modal';
import PresentModal from '../components/present-modal';

import { requestSendLog } from '../actions/send-log-api';
import { SNS_SHARE } from '../types/logs';

// import _ from 'lodash';

// material-ui components
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Grid from '@material-ui/core/Grid';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import CloseIcon from "@material-ui/icons/Close";

// import HomeIcon from '@material-ui/icons/Home';
import HamburgerIcon from '../assets/images/hamburgerIcon.svg';

import TutorialIcon from '../assets/images/tutorialIcon.svg';
import CouponIcon from '../assets/images/couponIcon.svg';
import InfoIcon from '@material-ui/icons/Info';
import TwitterIcon from '../assets/images/twitterIcon.svg';
import FacebookIcon from '../assets/images/facebookIcon.svg';
import QRCodeIcon from '../assets/images/QRCodeIcon.svg';
import GiftIcon from "@material-ui/icons/CardGiftcard";

// styles
import '../assets/styles/hamburger-menu.css';
import { withStyles } from '@material-ui/core/styles';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

//actions
import { setSoundControllerOpenState } from '../actions/sounds';
import { setHamburgerOpenState } from '../actions/hamburger';

const StyledDrawer = withStyles({
  paper: {
    backgroundColor: 'rgba(0,0,0,0.7)',
    position: 'fixed',
    zIndex: 1300,
  },
  root: {
    backgroundColor: 'rgba(0,0,255,0)',
    position: 'fixed',
    marginBottom: 150,
    zIndex: 1300,
  }
})(SwipeableDrawer)

const theme = createMuiTheme({
  overrides: {
    // Name of the component ⚛️ / style sheet
    MuiBackdrop: {
      // Name of the rule
      root: {
        // Some CSS
        backgroundColor: 'rgba(255,0,0,0)',
        position: 'fixed',
        marginBottom: 150,
        zIndex: 1300,
      },
    },
  },
  typography: {
    useNextVariants: true,
  },
});

const MEDIA_TWITTER = 'twitter'
const MEDIA_FACEBOOK = 'facebook'
// const shareLinks = {
//   [MEDIA_TWITTER]: 'https://twitter.com/share?text=【SEKAI NO OWARI SOUND RPG in SAPPORO SNOW FESTIVAL】%0a%0a特定の場所に行くことで音楽が解放される。%0aそんな新しい体験とSEKAI NO OWARIがコラボレート！最新アルバム収録曲を特別先行試聴できます。%0ahttps%3A%2F%2Fsound-rpg.com%2Fshare%0a%0a2月4日～11日　札幌エリア限定／期間限定%0a%0a&hashtags=セカオワ&url=https%3A%2F%2Fsound-rpg.com%2Fshare%0a',
//   [MEDIA_FACEBOOK]: 'https://www.facebook.com/sharer.php?kid_directed_site=0&sdk=joey&u=https%3A%2F%2Fsound-rpg.com%2Fshare',
// }
const shareLinks = {
  [MEDIA_TWITTER]: 'https://twitter.com/share?text=【SEKAI NO OWARI SOUND RPG in SAPPORO SNOW FESTIVAL】%0a特定の場所に行くことで音楽が解放される%0aそんな新しい体験とSEKAI NO OWARIがコラボレート！最新アルバム収録曲を特別先行試聴できます%0a2月4日～11日　札幌エリア限定／期間限定%0a&hashtags=セカオワ&url=https%3A%2F%2Fsound-rpg.com%2Fshare',
  [MEDIA_FACEBOOK]: 'https://www.facebook.com/sharer.php?kid_directed_site=0&sdk=joey&u=https%3A%2F%2Fsound-rpg.com%2Fshare',
}
class HamburgerMenu extends Component {
  constructor(props){
    super(props);
    this.menuItems = [
      {name: 'チュートリアル', icon: <img src={TutorialIcon} alt="" width="25" height="25" />, onClick: this.onClickTutorial},
      {name: 'クーポン', icon: <img src={CouponIcon} alt="" width="34" height="24"/>, onClick: this.onClickCoupon},
      {name: 'インフォメーション', icon: <InfoIcon style = {this.style}/>, onClick: this.onClickInfo},
      {name: 'プレゼント', icon: <GiftIcon style = {this.style}/>, onClick: this.onClickPresent},
      {name: 'Twitterでシェア', icon: <img src={TwitterIcon} alt="" width="25" height="25" />, onClick: this.onClickTwitter},
      {name: 'Facebookでシェア', icon: <img src={FacebookIcon} alt="" width="25" height="25"/>, onClick: this.onClickFacebook},
      {name: 'このサイトのQRコード', icon: <img src={QRCodeIcon} alt="" width="25" height="25"/>, onClick: this.onClickQRCode},
      {name: 'メニュー', icon: <InfoIcon style = {this.style}/>, onClick: this.onClickMenu},
      //'08': {name: 'ホーム', icon: <HomeIcon style = {this.style}/>, onClick: this.onClickHome},
      //'09': {name: 'マップ', icon: <HomeIcon style = {this.style}/>, onClick: this.onClickMap},
    ];
  }

  state = {
    isSmall:false,
    showModalTutorial: false,
    showModalCoupon: false,
    showModalInfo: false,
    showModalQRCode: false,
    showModalPresent: false,
  }

  onClickTutorial = () => {this.setState({showModalTutorial:true})}
  onClickCoupon = () => {this.setState({showModalCoupon:true})}
  onClickInfo = () => {this.setState({showModalInfo:true})}
  onClickPresent = () => {this.setState({showModalPresent:true})}

  //red: https://qiita.com/shuntaro_tamura/items/99adbe51132e0fb3c9e9
  onClickTwitter = () => { this.onClickShare(MEDIA_TWITTER) }
  onClickFacebook = () => { this.onClickShare(MEDIA_FACEBOOK) }
  onClickQRCode = () => {this.setState({showModalQRCode:true})}

  //onClickHome = () => {this.props.history.push('/')}
  //onClickMap = () => {this.props.history.push('/map')}
  onClickMenu = () => {this.props.history.push('/menu-test')}
  onClickModalStyleOne = () => { this.setState({showModalOne: true })}
  onClickModalStyleTwo = () => { this.setState({showModalTwo: true })}

  style = { fontSize: "24",color: "white" }

  onClickShare = (media) => {
    const params = { media };
    // API Call: SNS Share
    this.props.requestSendLog(SNS_SHARE, params);

    const path = shareLinks[media]
    window.open(path, '_blank');
  }

  componentDidMount(){
    //check screen size
    const screenWidth=window.parent.screen.width;

    if(screenWidth<=360){
      this.setState({isSmall: true});
    }else{
      this.setState({isSmall: false});
    }
  }

  componentDidUpdate(prevProps, prevState, snapShot){
    if(this.props.soundControllerOpenState !== prevProps.soundControllerOpenState){
      //if(this.props.soundControllerOpenState === true){
      if(this.props.soundControllerOpenState === true && this.props.hamburgerOpenState){
        this.closeHamburgerMenu();
      }
    }
    return null
  }

  createMenuItem(){
    return this.menuItems.map((menuItem, index) => {
      if(!this.props.settingData.appSetting.debugMode && (menuItem.name === 'メニュー')){
        return null
      }
      return (
        <ListItem className="listItem" key={index} value={index} onClick={this.onListItemClick}>
          <Grid container spacing={24}>
            <Grid item xs={2} align="center">
              {this.menuItems[index].icon}
            </Grid>
            <Grid item xs={10}>
              {this.menuItems[index].name}
            </Grid>
          </Grid>
        </ListItem>
      )
    })
  }

  onListItemClick = (event) => {
    this.closeHamburgerMenu()
    this.menuItems[event.currentTarget.value].onClick()
  }

  openHamburgerMenu = () => {
    if(this.props.soundControllerOpenState){
      this.closeSoundController();
    }
    this.setOpenState(true)
  }
  closeHamburgerMenu = () => {
    this.setOpenState(false)
  }
  toggleDrawer = () => {
    this.setOpenState(!this.props.hamburgerOpenState)
  }
  setOpenState = (openState) => {
    this.props.setHamburgerOpenState(openState)
  }

  // These are required by SwipableDrawer
  onClose = (event) => {
    this.closeHamburgerMenu()
  }
  onOpen = (event) => {}
  
  renderHamburger() {
    return (
        <div className="hamburger" onClick={this.openHamburgerMenu}>
          <img src={HamburgerIcon} alt="" width="40" height="40"/>
        </div>
    )
  }

  renderCloseButton() {
    const style = { fontSize: "40",color: "white" }
    return (
        <div className="closeIcon" onClick={this.closeHamburgerMenu} >
          <CloseIcon style={style} />
        </div>
    )
  }

  renderHelpButton() {
    return (
        <div className="help">
          <HelpModal isSmall={this.state.isSmall} />
        </div>
    )
  }

  renderStampRally(){
    const { hamburgerOpenState } = this.props
    if(!hamburgerOpenState){
      return(
        <StampRally soundList={this.props.soundList} isSmall={this.state.isSmall}/>
      )
    }
  }

  closeSoundController(){
    this.props.setSoundControllerOpenState(false);
  }

  //for modals
  onChangeTutorialState=(showModal) => {
    this.setState({showModalTutorial: showModal});
  }

  onChangeCouponState=(showModal) => {
    this.setState({showModalCoupon: showModal});
  }

  onChangePresentState=(showModal) => {
    this.setState({showModalPresent: showModal});
  }

  onChangeInfoState=(showModal) => {
    this.setState({showModalInfo: showModal});
  }

  onChangeQRCodeState=(showModal) => {
    this.setState({showModalQRCode: showModal});
  }

  render() {
    if (!this.props.isSupported) {
      return <Redirect to="/share" />
    }
    return (
      <div>
        {!this.props.hamburgerOpenState && (
          <div className="drawerClose">
            {this.renderHamburger()}
            {this.renderHelpButton()}
            <TutorialModal showModal ={this.state.showModalTutorial} callBackState = {this.onChangeTutorialState} isSmall = {this.state.isSmall}/>
            <CouponModal showModal ={this.state.showModalCoupon} callBackState = {this.onChangeCouponState} isSmall = {this.state.isSmall}/>
            <PresentModal showModal ={this.state.showModalPresent} callBackState = {this.onChangePresentState} isSmall = {this.state.isSmall}/>
            <InfoModal showModal ={this.state.showModalInfo} callBackState = {this.onChangeInfoState} isSmall = {this.state.isSmall}/>
            <QRCodeModal showModal ={this.state.showModalQRCode} callBackState = {this.onChangeQRCodeState} isSmall = {this.state.isSmall}/>
          </div>
        )}
        <div>
        <MuiThemeProvider theme={theme}>
          <StyledDrawer
            anchor="top"
            open={this.props.hamburgerOpenState}
            onClose={this.onClose}
            onOpen={this.onOpen}
          >
          <div className="drawerOpen">
            {this.renderCloseButton()}

            <List className="list">
              {this.createMenuItem()}
            </List>

          </div>
          </StyledDrawer>
        </MuiThemeProvider>
        </div>
        {this.renderStampRally()}
      </div>
    )
  }
}

//export default connect(state=>state)(HamburgerMenu)
//https://qiita.com/junara/items/a4a98c27dc23fd53ebb9
export default connect(state => state, {setHamburgerOpenState, setSoundControllerOpenState, requestSendLog })(withRouter(HamburgerMenu))
