import _ from 'lodash'
import React, { Component } from 'react';
// styles
import '../assets/styles/stamp-rally.css'
import soundItemActive from '../assets/images/sound-item-active.png';
import soundItemInactive from '../assets/images/sound-item-inactive.png';
//import Grid from '@material-ui/core/Grid';

class StampRally extends Component {
  constructor(props){
    super(props);
    this.state = {
      soundList: this.props.soundList,
      unlockedSongsCount: 0
    }

    this.allSongs=0;

    for(let key in this.state.soundList){
      const item=this.state.soundList[key];
      //console.log(item.key);
      if (!item.isBonus) {
        this.allSongs++;
      }
    }

    //getDerivedStateFromProps(this.state,this.state)

  }

  static getDerivedStateFromProps(nextProps, prevState) {
    /*
    if (!_.isEqual(nextProps.soundList, prevState.soundList)) {
      let unlockedSongsCount=0;

      for(let key in nextProps.soundList){
        const item=nextProps.soundList[key];
        console.log(item.key);
        if (!item.isBonus) {
          if(item.unlocked){
            unlockedSongsCount++;
          }
        }
      }
      console.log("count:"+unlockedSongsCount);
      return { soundList: nextProps.soundList, unlockedSongsCount }
    }
    return null
    */

   let unlockedSongsCount=0;

   for(let key in nextProps.soundList){
     const item=nextProps.soundList[key];
     //console.log(item.key);
     if (!item.isBonus) {
       if(item.unlocked){
         unlockedSongsCount++;
       }
     }
   }
   //console.log("count:"+unlockedSongsCount);
   return { soundList: nextProps.soundList, unlockedSongsCount }
  }

  stampImage(small) {
    const { soundList } = this.state;
    let style='stampImage';

    if(small){
      style='stampImageSmall';
    }else{
      style='stampImage';
    }

    let arr = _.map(soundList, (item, key) => {
      if (!item.isBonus) {
        return (
          <img
            key={item.key}
            // className={style + ' ' + (item.unlocked ? 'active' : 'inactive')}
            className={style}
            src={item.unlocked ? soundItemActive : soundItemInactive}
            alt={item.key}
            onClick={this._onClick}
            order={item.order}
          />
        )
      }
    });
    return _.sortBy(arr, ['props.order'])
  }

  render() {
    this.style="stampArea";
    this.stampStyle="stamp"
    this.textStyle="text";
    
    if(this.props.isSmall){
      this.style="stampAreaSmall";
      this.stampStyle="stampSmall"
      this.textStyle="textSmall"
    }

    return (
      <div className={this.style}>
        <div className={this.stampStyle}>
          {this.stampImage(this.props.isSmall)}
        </div>
        <div style={{'width':'10%','display':'inline-block','verticalAlign':'middle'}} className={this.textStyle}>
          {this.state.unlockedSongsCount}/{this.allSongs}
        </div>
      </div>
    )
  }
}

export default StampRally;
