import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
// componets
import Splash from '../components/splash-screen';
import TutorialPage from '../components/tutorial-page';
import GATracker from '../components/ga-tracker';
// styles
import '../assets/styles/main.css';

class Main extends Component {
  state = {
    splashRendered: true, 
    splashOpacity: 1,
    splashZIndex: 1,

    redirect: false,
    splashTime: 3500, // ms
    disableAutoSplash: true
  }

  componentDidMount() {
    this.splashTimeout();
  }

  renderSplash = () => {
    return (
      this.state.splashRendered ? <div
        className='mainLayers'
        style={{ zIndex: this.state.splashZIndex, opacity: this.state.splashOpacity
        }}
      >
        <Splash 
          disableAutoSplash={this.state.disableAutoSplash}
          splashTime={this.state.splashTime}
        />  {/* if disableAutoSplash true the redirect function in splash-screen.js won't work */}
      </div>
      : null
    )
  }

  splashTimeout = () => {
    if (process.env.REACT_APP_AUTO_SPLASH !== 'none') {
      this.timerHandle1 = setTimeout(this.changeScene, this.state.splashTime);
    }
  }

  changeScene = () => {
    if (this.props.sensorOn && !('isDefault' in this.props.geolocation)){
      this.setState({
        redirect: true
      })

      return;
    }

    this.setState({
      splashOpacity: 0
    })
    
    this.timerHandle2 = setTimeout(() => {
      this.unmountComponent(this.state.splashRendered, 'splashRendered');
    }, 700);
    return;
  }

  componentWillUnmount() {
    clearTimeout(this.timerHandle1)
    clearTimeout(this.timerHandle2)
  }

  unmountComponent = (renderedState, name) => {
    if (renderedState) {
      this.setState({
        [name]: false
      })
    }
  }

  buttonStyle = () => {
    if (this.state.splashRendered || !this.state.tutorialRendered){
      return { opacity: 0, visibility: 'hidden', transition: 'opacity .7s' };
    }
    return { position: 'relative', marginTop: '-80px', textAlign: 'center', width: '100vw', zIndex: 2 };
  }

  render() {
    if (!this.props.isSupported) {
      return <Redirect to="/share" />
    }
    if(this.state.redirect) {
      return <Redirect to={'/map'} />
    }

    return (
      <div>
        <GATracker pathname={this.props.location.pathname} />
        <div style={{height: `${window.innerHeight}px`}}>
          <link rel='stylesheet' href='https://fonts.googleapis.com/css?family=Roboto:300,400,500' />
          <link rel='stylesheet' href='https://fonts.googleapis.com/icon?family=Material+Icons' />
          <TutorialPage />
          {this.renderSplash()}
        </div>
      </div>
    )
  }
}

Main.propTypes = {
  isSupported: PropTypes.bool.isRequired,
  geolocation: PropTypes.object.isRequired,
  sensorOn: PropTypes.bool.isRequired,
};

export default Main;
