import React, { Component } from 'react';
import $ from 'jquery';
import CheckinAnimation from '../components/checkin-animation';
import ModalTemplate from './modal-template';
// images
// import sonyImg from '../assets/images/sony-image.png';
import checkInButtonImg from '../assets/images/check-in-button.png';
// styles
import '../assets/styles/areain-modal.css';

class AreainModal extends Component {
  state = {
    showModal: false
  }

  componentWillUnmount() {
    clearTimeout(this.timerHandle)
  }

  onCheckinAnimeDone = () => {
    const WAITING = 900
    this.timerHandle = setTimeout(() => {this.setState({ showModal: true })}, WAITING)
  }

  onCheckin = () => {
    this.props.callback()
  }

  formatContent = content => {
    return content.split('\n').map( (item,key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )}
    )
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    const { areaInfo } = this.props;

    let style = {
      height: `${window.innerHeight - (window.innerWidth - 30) * .5 - 85 - 15}px`,
      overflow: 'scroll',
    }

    if (!areaInfo) return null;

    return (
      <div>
        { !this.state.showModal && (
            <CheckinAnimation callback={this.onCheckinAnimeDone} />
        )}
        { this.state.showModal && (
          <ModalTemplate defaultShowModal={true}
                        noCloseButton
                        revealModal
                        noPadding={true}
                        shouldCloseOnOverlayClick={false} >
            <div style={{ width: '100vw - 30px', height: 'calc((100vw - 30px) * .5)' }} onClick={this.scrollTop}>
              <img src={areaInfo.image} alt='place' width='100%' style={{ pointerEvents: 'none' }}/>
            </div>
            <div className={`modal-content scroll-modal`} align='center' style={style}>
              <center style={{color: 'white', padding: '0 15px'}}>
                <div className='modal-miniTitle-heavy' style={{marginTop: '15px'}}>
                  {this.formatContent(areaInfo.name)}
                </div>
                <div className='modal-content-areaInfo'>
                  {this.formatContent(areaInfo.content)}
                </div>
                <div className='modal-content-openingHour'>
                {this.formatContent(areaInfo.openingHours)}
                </div>
                <img className='checkinButton' src={checkInButtonImg} onClick={this.onCheckin} alt='checkin' />
              </center>
            </div>
          </ModalTemplate>
        )}
      </div>
    )
  }
}
export default AreainModal
