import React, { Component } from 'react';
// import { connect } from 'react-redux';
// import { withRouter } from 'react-router-dom'; // ref: https://tylermcginnis.com/react-router-programmatically-navigate/
import Slider from 'react-slick'; // ref: https://www.npmjs.com/package/react-slick
// import { setSensorOn } from '../actions/sensor-datas';
// components
import TutorialContents from '../components/tutorial-contents';
// images
import appBackground from '../assets/images/app-background.jpg';
import tutorialButton from '../assets/images/tutorial-button.png';

// styles
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../assets/styles/tutorial-page.css';

const CENTER_BETWEEN_HEADER_FOOTER = 50;

class TutorialPage extends Component {
  state = {
    buttonDisplay: {
      visibility: 'hidden',
      opacity: '0'
    },
    opacity: 0,

    tutorialRendered: true,
    tutorialOpacity: 1,
    tutorialZIndex: 1,

    slideIndex: 0
  }

  appBackgroundSrc = appBackground;

  componentDidMount() {
    this.changeOpacity();
    this.renderArrowImage('slick-next', 0);
    this.renderArrowImage('slick-prev', 180);
  }

  renderArrowImage = ( className, deg) => {
    const arrowNext = document.createElement('img');
    arrowNext.setAttribute('class', 'arrowImg');
    arrowNext.setAttribute('src', `${tutorialButton}`);
    arrowNext.style.transform = `rotate(${deg}deg)`;
    document.getElementsByClassName(className)[0].appendChild(arrowNext);
  }

  changeOpacity = () => {
    this.setState({
      opacity: 1
    })
  }

  // displayButton(){
  //   if ( 'isDefault' in this.props.geolocation ) {
  //     return false;
  //   };
  // }

  contentStyle = {
    marginTop: `${(window.innerHeight + CENTER_BETWEEN_HEADER_FOOTER) * .5}px`
  }

  skipSlide = e => {
    this.setState({
      slideIndex: e
    })
    this.slider.slickGoTo(e)
  }

  render() {
    let settings = {
      dots: true,
      arrows: true,
      infinite: false,
      // fade: true,
      cssEase: 'cubic-bezier(0.39, 0.575, 0.565, 1)',
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      initialSlide: 0,

      // centerMode: true,

      beforeChange: (current, next) => this.setState({ slideIndex: next })
    };

    return (this.state.tutorialRendered ? <div
      className='mainLayers'
      style={{
        position: 'fixed',
        zIndex: this.state.tutorialZIndex, opacity: this.state.tutorialOpacity
      }}
    >
      <div className='fade-in backGradient'
        style={{
          backgroundImage: `url(${this.appBackgroundSrc})`,
          height: `${window.innerHeight}px`,
          width: '100vw',
          transition: 'opacity .7s',
          opacity: this.state.opacity,
        }}
      >
        <div className='titleContainer'>
          チュートリアル
        </div>
        {/* {(this.state.slideIndex === 0) && (
          <div className='swipeText'>
            右へスワイプして進む
          </div>
        )} */}
        <Slider ref={slider => (this.slider = slider)}{...settings}>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={0} />
            </div>
          </div>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={1} />
            </div>
          </div>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={2} />
            </div>
          </div>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={3} />
            </div>
          </div>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={4} />
            </div>
          </div>
          <div> {/* page container */}
            <div className='contentContainer' style={this.contentStyle}>
              <TutorialContents slideNumber={5} />
              <TutorialContents slideNumber={6} />
            </div>
          </div>
        </Slider>
        { (this.state.slideIndex !== 5) && (
          <div className='tutorialFooter' onClick={ () => {this.skipSlide(5)}}>
            チュートリアルをスキップ
          </div>
        )}
      </div>
    </div>
    : null
    )
  }
}

// export default connect(state => state, { setSensorOn })(withRouter(TutorialPage));
export default TutorialPage;
