// ref: https://qiita.com/chiyoyo/items/b10bd3864f3ce5c56291
class DistanceFromCoords {
  /**
   * ２地点間の距離(m)を求める
   * ヒュベニの公式から求めるバージョン
   *
   * @param float lat1 緯度１
   * @param float lon1 経度１
   * @param float lat2 緯度２
   * @param float lon2 経度２
   * @param boolean mode 測地系 true:世界 false:日本
   * @return float 距離(m)
   */
  getDist(lat1, lon1, lat2, lon2, mode = true) {
    // 緯度経度をラジアンに変換
    let radLat1 = lat1 * Math.PI / 180; // 緯度１
    let radLon1 = lon1 * Math.PI / 180; // 経度１
    let radLat2 = lat2 * Math.PI / 180; // 緯度２
    let radLon2 = lon2 * Math.PI / 180; // 経度２

    // 緯度差
    let radLatDiff = radLat1 - radLat2;

    // 経度差算
    let radLonDiff = radLon1 - radLon2;

    // 平均緯度
    let radLatAve = (radLat1 + radLat2) / 2.0;

    // 測地系による値の違い
    let a = mode ? 6378137.0 : 6377397.155; // 赤道半径
    // let b = mode ? 6356752.314140356 : 6356078.963; // 極半径

    //e2 = (a*a - b*b) / (a*a);
    let e2 = mode ? 0.00669438002301188 : 0.00667436061028297; // 第一離心率^2
    //a1e2 = a * (1 - e2);
    let a1e2 = mode ? 6335439.32708317 : 6334832.10663254; // 赤道上の子午線曲率半径

    let sinLat = Math.sin(radLatAve);
    let W2 = 1.0 - e2 * (sinLat * sinLat);
    let M = a1e2 / (Math.sqrt(W2) * W2); // 子午線曲率半径M
    let N = a / Math.sqrt(W2); // 卯酉線曲率半径

    let t1 = M * radLatDiff;
    let t2 = N * Math.cos(radLatAve) * radLonDiff;

    let dist = Math.sqrt((t1 * t1) + (t2 * t2));

    return dist;
  }

  /**
   *  position が エリア内に入っている場合、 areas の key, distance を近い順にすべて返却する
   *  @param object position: expects lat, lng
   *  @param object areas:    expects form of { key: {lat, lng} }
  */
  inAreas(position, areas) {
    let inAreas = [];
    for (let key in areas) {
      let distance = this.getDist(position.lat, position.lng, areas[key].lat, areas[key].lng);
      if (distance <= areas[key].radius) {
        inAreas.push({ key, distance });
      };
    }

    inAreas.sort(function(a, b){
      if( a.distance < b.distance ) return -1;
      if( a.distance > b.distance ) return 1;
      return 0;
    });
    return inAreas;
  }

  /**
   *  position が エリア内に入っている場合、一番近いエリアのkeyを返却
   *  @param object position: expects lat, lng
   *  @param object areas:    expects form of { key: {lat, lng} }
  */
  nearestArea(position, areas) {
    const inAreas = this.inAreas(position, areas)
    if (inAreas.length === 0) {
      return false
    }
    return inAreas[0].key
  }

}
export default DistanceFromCoords;
