import _ from 'lodash'
import React, {Component} from 'react'
import { divStyle, imgStyle } from './style-current-pin.js'
import pin from '../../assets/images/pin-current.png'

export default class UserMarker extends Component {
  render() {
    const activeStyle = _.clone(divStyle)
    activeStyle.transform = `rotate(${this.props.rotate}deg)`
    return (
      <div style={activeStyle}>
        <img src={pin} alt='Current Position' style={imgStyle} />
      </div>
    )
  }
}
