import React, { Component } from 'react';
import $ from 'jquery';
import ModalTemplate from './modal-template';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';


class CouponGetModal extends Component {
  onModalClosed = () => {
    if (this.props.onModalClosed) {
      this.props.onModalClosed()
    }
  }

  couponCardRender(coupon){
    return (
      <div className="couponViewer">
      <Card >
        <div className="couponCard">
        <Typography variant="h6" align="center">
            {this.formatContent(coupon.name)}
        </Typography>
        <div className="couponImageCanvas">
          <img src={coupon.image} alt="CouponImage" className="couponImage" style={{ pointerEvents: 'none' }}/>
        </div>
        <Grid container justify="center">
        <Typography align="center">
            {this.formatContent(coupon.description)}
        </Typography>
            <img src={coupon.logo} alt="logo" width="30%" height="30%" style={{ pointerEvents: 'none' }}/>
        </Grid>
        </div>
      </Card>
    </div>
    )
  }

  formatContent = (content) => {
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    })
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    const {couponInfo } = this.props;

    let style = {
      height: `${window.innerHeight - 220}px`,
      overflow: 'scroll',
      textAlign: 'justify'
    }

    return (
      <ModalTemplate defaultShowModal={true}
                    smallGlow
                    callbackClose={this.onModalClosed} >
  
        <div className='modal-title' onClick={this.scrollTop}>
          Got a new Coupon!
        </div>
        <div className='modal-content'>
          <div align='center'>
          <br />
          入手したクーポンは右上メニューの<br />
          「クーポン」から利用できます。
          </div>
          <div className={`modal-content scroll-modal`} style={style}>
            {this.couponCardRender(couponInfo)}
          </div>
        </div>
      </ModalTemplate>
    )
  }
}
export default(CouponGetModal)
