import { createStore, combineReducers, applyMiddleware } from 'redux'
// reducers
import settingData       from './reducers/reducer-setting-data'
import sensorOn          from './reducers/reducer-sensor-on'
import currentArea       from './reducers/reducer-current-area'
// import geolocation       from './reducers/reducer-geolocation'
import isIphone          from './reducers/reducer-is-iphone'
import isIpad            from './reducers/reducer-is-ipad'
import isMobile          from './reducers/reducer-is-mobile'
import isSupported       from './reducers/reducer-is-supported'
import browserType       from './reducers/reducer-browser-type'
// import compassHeading    from './reducers/reducer-compass-heading'
import rotation          from './reducers/reducer-rotation'
// import deviceOrientation from './reducers/reducer-device-orientation'
// import soundProgress     from './reducers/reducer-sound-progress'
import logApiState       from './reducers/reducer-log-api-state'
import inEventArea       from './reducers/reducer-in-event-area'
import soundComplete     from './reducers/reducer-sound-complete'
import soundControllerOpenState from './reducers/reducer-sound-controller-open-state'
import hamburgerOpenState from './reducers/reducer-hamburger-open-state'
import presentObtain     from './reducers/reducer-present-obtain'


import thunk from 'redux-thunk'
const reducer = combineReducers({
    sensorOn,
    // geolocation,
    isIphone,
    isIpad,
    isMobile,
    isSupported,
    browserType,
    settingData,
    currentArea,
    // compassHeading,
    rotation,
    // deviceOrientation,
    // soundProgress,
    logApiState,
    inEventArea,
    soundComplete,
    presentObtain,
    soundControllerOpenState,
    hamburgerOpenState,
})
const store = createStore(
    reducer,
    applyMiddleware(thunk)
)
export default store;
