const ax = new AxiosUtils()
const constDispatcher = (endpoint, params, type) => {
  return dispatch => {
    ax.post(endpoint, params).then((response) => {
      dispatch({
        type,
        payload: response.data
      })
    }).catch((response) => {
      dispatch({
        type,
        error: response
      })
    })
  }
}

export const send_initApp = (params = {}) => {
  return constDispatcher('init_app', params, DEACTIVATE_INIT_APP)
}
export const send_areaIn = (params = {}) => {
  return constDispatcher('area_in', params, DEACTIVATE_AREA_IN)
}
export const send_snsShare = (params = {}) => {
  return constDispatcher('sns_share', params, DEACTIVATE_SNS_SHARE)
}
export const send_soundAction = (params = {}) => {
  return constDispatcher('sound_actions', params, DEACTIVATE_SOUND_ACTION)
}
export const send_soundReaction = (params = {}) => {
  return constDispatcher('sound_reaction', params, DEACTIVATE_SOUND_REACTION)
}
export const send_coupon = (params = {}) => {
  return constDispatcher('coupon', params, DEACTIVATE_COUPON)
}
export const send_logAny = (params = {}) => {
  return constDispatcher('log', params, DEACTIVATE_LOG_ANY)
}
