import {
  ACTIVATE_INIT_APP, DEACTIVATE_INIT_APP,
  ACTIVATE_AREA_IN, DEACTIVATE_AREA_IN,
  ACTIVATE_SNS_SHARE, DEACTIVATE_SNS_SHARE,
  ACTIVATE_SOUND_ACTION, DEACTIVATE_SOUND_ACTION,
  ACTIVATE_SOUND_REACTION, DEACTIVATE_SOUND_REACTION,
  ACTIVATE_COUPON, DEACTIVATE_COUPON,
  ACTIVATE_LOG_ANY, DEACTIVATE_LOG_ANY,
} from '../types/logs'

const inactiveApiState = {
    onCalling: false,
    params: null
}
const activeApiState = {
  onCalling: true,
  params: null
}

const initState = {
  initApp:       inactiveApiState,
  areaIn:        inactiveApiState,
  snsShare:      inactiveApiState,
  soundAction:   inactiveApiState,
  soundReaction: inactiveApiState,
  coupon:        inactiveApiState,
  logAny:        inactiveApiState
}

export default (state = initState, action) => {
  switch (action.type) {
    case ACTIVATE_INIT_APP:
      // activeApiState.params = action.payload;
      // return { ...state, initApp: activeApiState};
      return { ...state, initApp: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_INIT_APP:
      return { ...state, initApp: inactiveApiState};

    case ACTIVATE_AREA_IN:
      return { ...state, areaIn: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_AREA_IN:
      return { ...state, areaIn: inactiveApiState};

    case ACTIVATE_SNS_SHARE:
      return { ...state, snsShare: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_SNS_SHARE:
      return { ...state, snsShare: inactiveApiState};

    case ACTIVATE_SOUND_ACTION:
      return { ...state, soundAction: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_SOUND_ACTION:
      return { ...state, soundAction: inactiveApiState};

    case ACTIVATE_SOUND_REACTION:
      return { ...state, soundReaction: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_SOUND_REACTION:
      return { ...state, soundReaction: inactiveApiState};

    case ACTIVATE_COUPON:
      return { ...state, coupon: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_COUPON:
      return { ...state, coupon: inactiveApiState};

    case ACTIVATE_LOG_ANY:
      return { ...state, logAny: { ...activeApiState, params: action.payload }};
    case DEACTIVATE_LOG_ANY:
      return { ...state, logAny: inactiveApiState};

    default:
      return state
  }
}
