import { INIT_SETTINGS, CHANGE_SOUND_LOCK, LOCK_ALL_SOUNDS, SET_SOUND_COMPLETE, REMOVE_SOUND_COMPLETE, CHANGE_COUPON_VISIBLE, CHANGE_COUPON_USED, SOUND_LIST_DATA_NAME, COUPON_LIST_DATA_NAME, SET_PRESENT_OBTAIN, REMOVE_PRESENT_OBTAIN } from '../types/settings'

export const initSettings = () => {
  // TODO: app-setting-ebisu.json is only for testing purpose
  const APP_SETTING_PATH = '/assets/jsons/app-setting.json'
  // const APP_SETTING_PATH = '/assets/jsons/app-setting-ebisu.json'
  const SOUND_LIST_PATH = '/assets/jsons/sound-list.json'
  const COUPON_LIST_PATH = '/assets/jsons/coupon-list.json'

  const appSetting = fetch(APP_SETTING_PATH).then((response) => {
                        return response.json()
                     }).then((parsedData) => {
                       return parsedData
                     }).catch((e) => {
                       console.error(e)
                     })

  const soundList = fetch(SOUND_LIST_PATH).then((response) => {
                       return response.json()
                    }).then((parsedData) => {
                      return parsedData
                    }).catch((e) => {
                      console.error(e)
                    })

  const couponList = fetch(COUPON_LIST_PATH).then((response) => {
                      return response.json()
                   }).then((parsedData) => {
                     return parsedData
                   }).catch((e) => {
                     console.error(e)
                   })

  return dispatch => {
    Promise.all([appSetting, soundList, couponList]).then((values) => {
      dispatch({
        type: INIT_SETTINGS,
        payload: {
          appSetting: values[0],
          [SOUND_LIST_DATA_NAME]: values[1],
          [COUPON_LIST_DATA_NAME]: values[2]
        }
      })
    })
  }
}

export const changeSoundLock = (soundId, unlocked = true) => {
  return {
    type: CHANGE_SOUND_LOCK,
    payload: { soundId, unlocked }
  }
}

export const lockAllSounds = () => {
  return {
    type: LOCK_ALL_SOUNDS,
    payload: null
  }
}
export const setSoundComplete = () => {
  return {
    type: SET_SOUND_COMPLETE,
    payload: null
  }
}

export const removeSoundComplete = () => {
  return {
    type: REMOVE_SOUND_COMPLETE,
    payload: null
  }
}

export const changeCouponVisible = (couponId_visible, visible = true) => {
  return {
    type: CHANGE_COUPON_VISIBLE,
    payload : {couponId_visible, visible }
  }
}

export const changeCouponUsed = (couponId_used, used = true) => {
  return {
    type: CHANGE_COUPON_USED,
    payload : {couponId_used, used }
  }
}

export const setPresentObtain = () => {
  return {
    type: SET_PRESENT_OBTAIN,
    payload: null
  }
}

export const removePresentObtain = () => {
  return {
    type: REMOVE_PRESENT_OBTAIN,
    payload: null
  }
}

