import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux'

import SensorDataHandler from '../containers/sensor-data-handler'
import Geolocation from '../components/geolocation'
import { setIsIphone, setIsIpad, setIsMobile, setIsSupported, setBrowserType } from '../actions/sensor-datas'

import { BROWSER_MSIE, BROWSER_EDGE, BROWSER_CHROME, BROWSER_SAFARI, BROWSER_OPERA, BROWSER_FIREFIX } from '../types/browser.js'

class SensorHandleController extends Component {
  state = {
    sensorOn: false,
    isMobile: null,
    isSupported: null
  }

  componentDidMount(){
    this.platformInfo = window.navigator.userAgent.toLowerCase()
    this.props.setIsIphone(this.isIphone())
    this.props.setIsIpad(this.isIpad())
    this.setIsMobile()
    this.setIsSupported()
    this.props.setBrowserType(this.checkBrowserType(this.platformInfo))
  }

  setIsMobile = () => {
    const isMobile = this.isMobile()
    this.setState({ isMobile })
    this.props.setIsMobile(isMobile)
  }
  setIsSupported = () => {
    const isSupported = this.isSupported()
    this.setState({ isSupported })
    this.props.setIsSupported(isSupported)
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.sensorOn !== prevState.sensorOn) {
      return { sensorOn: nextProps.sensorOn }
    }
    return null
  }

  isSupported = () => {
    let isSupported = false

    // let browserType = this.checkBrowserType(this.platformInfo)
    // if (this.isAndroid()) {
    //   if(browserType === 'chrome') {
    //     isSupported = true
    //   }
    // } else if (this.isIphone()) {
    //   if(browserType === 'safari' ){
    //     isSupported = true
    //   }
    // }

    isSupported = (this.isMobile() || this.isIpad())
    return isSupported
  }
  isMobile = () => {
    return (this.isAndroid() || this.isIphone())
  }
  isIpad = () => {
    return (this.platformInfo.indexOf('ipad') > -1)
  }
  isIphone = () => {
    return (this.platformInfo.indexOf('iphone') > -1)
  }
  isAndroid = () => {
    return (this.platformInfo.indexOf('android') > -1)
  }
  checkBrowserType = (agent) => {
    let result

    if (agent.indexOf(BROWSER_MSIE) > -1 || agent.indexOf('trident/7') > -1) {
      result = BROWSER_MSIE
    } else if (agent.indexOf(BROWSER_EDGE) > -1) {
      result = BROWSER_EDGE
    } else if (agent.indexOf(BROWSER_CHROME) > -1 || (agent.indexOf('crios') > -1)) {
      result = BROWSER_CHROME
    } else if (agent.indexOf(BROWSER_SAFARI) > -1) {
      result = BROWSER_SAFARI
    } else if (agent.indexOf(BROWSER_OPERA) > -1) {
      result = BROWSER_OPERA
    } else if (agent.indexOf(BROWSER_FIREFIX) > -1) {
      result = BROWSER_FIREFIX
    }
    return result
  }

  onChangeGeolocation = (geolocation) => {
    this.props.callbackGeolocation(geolocation)
  }
  onChangeCompassHeading = (compassHeading) => {
    this.props.callbackCompassHeading(compassHeading)
  }
  onChangeDeviceOrientation = (deviceOrientation) => {
    this.props.callbackDeviceOrientation(deviceOrientation)
  }

  render () {
    const { isMobile, isSupported } = this.state
    if ((isMobile === null) || (isSupported === null)) {
      return null
    }

    const { settingData } = this.props
    return (
      <div>
        { this.state.sensorOn && (
          <Fragment>
            <SensorDataHandler
              callbackCompassHeading={this.onChangeCompassHeading}
              callbackDeviceOrientation={this.onChangeDeviceOrientation}
              isSupported={isSupported}
            />
            <Geolocation
              callbackGeolocation={this.onChangeGeolocation}
              useGeolocation={settingData.appSetting.useGeolocation}
              startPosition={settingData.appSetting.startPosition}
            />
          </Fragment>
        )}      
        {this.props.children}
      </div>
    )
  }
}

SensorHandleController.propTypes = {
  callbackGeolocation: PropTypes.func.isRequired,
  callbackCompassHeading: PropTypes.func.isRequired,
  callbackDeviceOrientation: PropTypes.func.isRequired,
};

export default connect(state => state, { setIsIphone, setIsIpad,
                                         setIsMobile, setIsSupported, setBrowserType })(SensorHandleController)
