import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import HamburgerMenu from '../components/hamburger-menu';
import { lockAllSounds, removeSoundComplete } from '../actions/settings';
import Button from '@material-ui/core/Button'

class RemoveAllStorage extends Component {
  onResetAllSound = () => {
    this.props.lockAllSounds()
    this.props.removeSoundComplete()
  }

  onResetAll = () => {
    localStorage.clear()
  }

  render() {
    return (
      <div>
        { !this.props.asParts && (
          <div>
            <HamburgerMenu soundList={this.props.settingData.soundList} />
            <br /><br /><br /><br />
          </div>
        )}
        <p>
          <Button variant='contained'  onClick={this.onResetAllSound}>
            Reset All Sound Locks
          </Button>
        </p>
        <p>
          <Button variant='contained'  onClick={this.onResetAll}>
            Remove All Storage
          </Button>
        </p>
        <p>
          <Button variant='contained' >
            <Link to="/map">Back to play</Link>
          </Button>
        </p>
      </div>
    )
  }
}

export default connect(state => state, { lockAllSounds, removeSoundComplete })(RemoveAllStorage)
