import { SET_CURRENT_AREA, RESET_CURRENT_AREA, SET_IN_EVENT_AREA } from '../types/statuses'

export const setCurrentArea = (areaId) => {
  return {
    type: SET_CURRENT_AREA,
    payload: areaId
  }
}
export const resetCurrentArea = () => {
  return {
    type: RESET_CURRENT_AREA,
    payload: null
  }
}
export const setInEventArea = (inEventArea) => {
  return {
    type: SET_IN_EVENT_AREA,
    payload: inEventArea
  }
}
