import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
// images
import appTitle from '../assets/images/app-title.png';
import appBackground from '../assets/images/app-background.jpg';
import cautionMessageImg from '../assets/images/caution-message.png';
import sponsorFooterImg from '../assets/images/sponsors-logo.png';
// styles
import '../assets/styles/splash.css';

class SplashScreen extends Component {
  state = {
    opacity: 1,
    disableAutoSplash: this.props.disableAutoSplash, // check main.js
    splashTime: this.props.splashTime // check main.js
  };

  appBackgroundSrc = appBackground; // add your splash screen image here
  splashTitleSrc = appTitle; // add your splash title image here

  componentDidMount() {
    if (process.env.REACT_APP_AUTO_SPLASH !== 'none' && !this.state.disableAutoSplash) {
      this.timerHandle = setTimeout(this.changeState, this.state.splashTime);
    }
  }
  componentWillUnmount() {
    clearTimeout(this.timerHandle)
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.startedFading && !prevState.toNextScene) {
      return {
        toNextScene: true
      }
    }
    return null
  }

  changeState = () => {
    this.setState({
      startedFading: true,
      opacity: 0
    })
  }

  render() {
    if (this.state.toNextScene === true) {
      // put next scene route
      const pathTo = (this.props.sensorOn && !('isDefault' in this.props.geolocation)) ? 'map' : 'tutorial';
      return <Redirect to={`/${pathTo}`} />
    }
      
    return (
      <div className='splashImageContainer'
        style={{
          backgroundImage: `url(${this.appBackgroundSrc})`,
          height: `${window.innerHeight}px`,
          opacity: this.state.opacity,
        }}
      >
        <div className='cautionMessage'>
          <img src={cautionMessageImg} alt='caution' style={{width: `${window.innerWidth - 30}px`}} />
        </div>
        
        <div className='splashTitleContainer'
          style={{
            paddingTop: `${window.innerHeight * .5}px`,
          }}>
          <img className='splashTitleImg' src={this.splashTitleSrc} alt='Title' />
        </div>

        <img className='sponsorFooter' src={sponsorFooterImg} alt='sponsors' />
      </div>
    )
  }
}
export default SplashScreen