import React, { Component } from 'react';
import ModalTemplate from './modal-template';
import { connect } from 'react-redux';
import _ from 'lodash';
import $ from 'jquery';
//aciton
import {changeCouponUsed} from '../actions/settings';
import { requestSendLog } from '../actions/send-log-api';
import { COUPON } from '../types/logs';
//assets
import usedIcon from '../assets/images/usedIcon.png'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import '../assets/styles/coupon-modal.css';




class CouponModal extends Component {
  state = {
    dialogOpen: false,
    clickedCoupon : null
  }

  shouldComponentUpdate(nextProps, prevState) {
    if(nextProps.showModal !== this.props.showModal){
      return true
    }
    if(this.state.dialogOpen !== prevState.dialogOpen){
      return true
    }
    return false
  }


  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  handleDialogOpen = (couponId) => {
    this.setState({
      dialogOpen: true,
      clickedCoupon: couponId
    });
  };

  handleDialogCloseAgree = () => {
    // API Call: Coupon consume
    const params = { coupon_id: this.state.clickedCoupon, action: 'consume' }
    this.props.requestSendLog(COUPON, params)

    this.props.changeCouponUsed(this.state.clickedCoupon);
    this.setState({
      dialogOpen: false,
      clickedCoupon: null
    });
  };

  handleDialogCloseDisagree = () => {
    this.setState({
      dialogOpen: false,
      clickedCoupon: null
    });
  };

  emptyCounponJSX(){
    return(
      <div className="emptyCoupon">
      使用可能なクーポンがありません
      </div>
    )
  }

  couponCardJSX(coupon, key) {
    return (
      <div key={coupon.order} className="couponViewer">
      <Card >
        <div className="couponCard">
        <Typography variant="h6" align="center">
            {this.formatContent(coupon.name)}
        </Typography>
        <div className="couponImageCanvas">
          {this.usedIconRender(coupon)}
          <img src={coupon.image} alt="CouponImage" className="couponImage" style={{ pointerEvents: 'none' }}/>
        </div>
        <Grid container justify="center">
          <CardActions>
            <Button variant="contained" onClick={()=>{this.handleDialogOpen(coupon.key)}} disabled={coupon.used}>
              クーポンを使用済みにする
            </Button>
          </CardActions>
          {/* <CardContent> */}
            <img src={coupon.logo} alt="logo" width="30%" height="30%" style={{ pointerEvents: 'none' }}/>
          {/* </CardContent> */}
        </Grid>
        </div>
      </Card>
    {this.dialogRender()}
    </div>
    )
  }

  dialogRender(){
    return(
      <Dialog
      open={this.state.dialogOpen}
      onClose={this.handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      style={{zIndex:'9999'}}
    >
      <DialogTitle id="alert-dialog-title">{"クーポンを使用済みにしますか？"}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          この操作は取り消しできません
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={this.handleDialogCloseDisagree} color="primary">
          キャンセル
        </Button>
        <Button onClick={this.handleDialogCloseAgree} color="primary">
          OK
        </Button>
      </DialogActions>
    </Dialog>
    )
  }

  usedIconRender(coupon){
    if(coupon.used){
      return (
        <img src={usedIcon} alt="usedIcon" className="usedIcon" style={{ pointerEvents: 'none' }}/>
      )
    }
    return null
  }

  couponCardRender(){
    let {couponList} = this.props.settingData;
    let visibleCouponList =_.filter(couponList, 'visible');
    if(visibleCouponList.length === 0){
      return(this.emptyCounponJSX())
    }
    let couponCardArr = _.map(visibleCouponList, (coupon,key) => {return (this.couponCardJSX(coupon, key))});
      return _.sortBy(couponCardArr, ['key'])
  }

  couponCardRender2(){
    let {couponList} = this.props.settingData;

    let checkCoupon = false;
    let couponCardArr = _.map(couponList, (coupon,key) =>{
      if(coupon.visible){
        checkCoupon = true;
        return (this.couponCardJSX(coupon, key));
      }
    });
    if(!checkCoupon){
      return(this.emptyCounponJSX())
    }
    return _.sortBy(couponCardArr, ['key'])
  }

  formatContent = (content) => {
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    })
  }

  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render() {
    let style = {
      //height: `${window.innerHeight - (this.props.isSmall ? 175 : 235)}px`,
      height: `${window.innerHeight - 235}px`,
      overflow: 'scroll'
    }

    return (
      <div>
      <ModalTemplate showModal={this.props.showModal}
        callBackState={this.handleModal}
        shouldCloseOnOverlayClick={true}
        isShort={false}
        isSmall={this.props.isSmall}
        revealModal
        >
          <div className="modal-miniTitle" onClick={this.scrollTop}>
          クーポン<br />
          Coupon
        </div>
        <div className='modal-content' style={{textAlign:"center",marginBottom:'10px'}}>
          クーポンをご利用の際は店員の方に<br/>
          この画面をお見せください
        </div>
        <div className={`modal-content scroll-modal`} style={style}>
          {this.couponCardRender()}
        </div>
      </ModalTemplate>
      </div>
    )
  }
}
export default connect(state => state, {changeCouponUsed, requestSendLog})(CouponModal)
