// import _ from 'lodash'
import { Component } from 'react'
import PropTypes from 'prop-types';
// redux
import { connect } from 'react-redux'
import {INIT_APP, AREA_IN, SNS_SHARE, SOUND_ACTION, SOUND_REACTION, COUPON, LOG_ANY} from '../types/logs'
import {send_initApp, send_areaIn, send_snsShare, send_soundAction, send_soundReaction, send_coupon, send_logAny} from '../actions/log-transmitter'
import {requestSendLog} from '../actions/send-log-api'

class LogHandler extends Component{
  state = {
    [INIT_APP]: false,
    [AREA_IN]:  false,
    [SNS_SHARE]: false,
    [SOUND_ACTION]: false,
    [SOUND_REACTION]: false,
    [COUPON]: false,
    [LOG_ANY]: false
  }

  componentDidMount() {
    // API Call: Init App
    this.props.requestSendLog(INIT_APP)
  }

  static getDerivedStateFromProps(props, state){
    const retState = {};
    for(let logtype of Object.keys(state)){
      retState[logtype] = LogHandler.checkOnCalling(logtype, props, state);
    }
    return retState;
  }

  static checkOnCalling(_logType, _props, _state){
    if (!_props.logApiState[_logType].onCalling) {
      return false;
    }

    if (!_state[_logType]) {
      let params = LogHandler.generateParams(_logType, _props);
      _props['send_' + _logType](params);
    }
    // Important: You always have to return true, when props's onCalling is true
    return true;
  }

  static generateParams(_logType, _props){
    let params = {}
    if (!('isDefault' in _props.geolocation)) {
      params = {
        'lat': _props.geolocation.pos.lat,
        'lon': _props.geolocation.pos.lng,
        'geo_accuracy': _props.geolocation.pos.geo_accuracy,
        'alt': _props.geolocation.pos.alt,
        'alt_accuracy': _props.geolocation.pos.alt_accuracy,
      };
    }

    switch(_logType){
      default:
        return null;

      case INIT_APP:
        return params;

      case AREA_IN:
        params['area_id'] = _props.currentArea;
        return params;

      case SNS_SHARE:
        params['media'] = _props.logApiState.snsShare.params.media;
        return params;

      case SOUND_ACTION:
        params['action'] = _props.logApiState.soundAction.params.action;
        params['sound_id'] = _props.logApiState.soundAction.params.sound_id;
        if(params.action === 'end'){
          params['active_range'] = _props.logApiState.soundAction.params.active_range
        }
        return params;

      case SOUND_REACTION:
        params['sound_id'] = _props.logApiState.soundReaction.params.sound_id;
        params['action'] = _props.logApiState.soundReaction.params.action;
        params['sound_progress'] = _props.logApiState.soundReaction.params.sound_progress;
        return params;

      case COUPON:
        params['coupon_id'] = _props.logApiState.coupon.params.coupon_id;
        params['action'] = _props.logApiState.coupon.params.action;
        return params;

      case LOG_ANY:
        for (let i in _props.logApiState.logAny.params) {
          let flg = true
          for (let j in params) {
            if (i === j) {
              flg = false
            }
          }
          if (flg) {
            params[i] = _props.logApiState.logAny.params[i]
          }
        }
        if (!('action' in params)) {
          // Because it's required
          params['action'] = 'default';
        }
        return params;
    }
  }

  render() {
    return this.props.children;
  }
}

LogHandler.propTypes = {
  geolocation: PropTypes.object.isRequired,
};

export default connect(state => state,{
  send_initApp,
  send_areaIn,
  send_snsShare,
  send_soundAction,
  send_soundReaction,
  send_coupon,
  send_logAny,
  requestSendLog
})(LogHandler)
