import { Component } from 'react'
import PropTypes from 'prop-types';

class Geolocation extends Component {
  state = {
    geolocation: {
      isDefault: true,
      pos: {
        lat: 0,
        lng: 0,
        pos_accuracy: 0
      },
      alt: {
        alt: 0,
        alt_accuracy: 0
      }
    }
  }

  componentDidMount(){
    // Initialize geolocation
    if (this.props.useGeolocation) {
      this.initGeolocation()
      this.initWatchPosition()
    } else {
      this.setGeolocation({
        pos: this.props.startPosition,
        alt: {
          alt: 0,
          alt_accuracy: 0
        }
      })
    }
  }

  componentWillUnmount() {
    window.navigator.geolocation.clearWatch(this.watchId);
  }
  
  initWatchPosition = () => {
    if ('geolocation' in window.navigator){
      this.watchId = window.navigator.geolocation.watchPosition(
        (event) => this.moveGeolocation(event),
        null,
        {enableHighAccuracy:true, maximumAge: 3000, timeout: 30000}
      )
    } else {
      console.error('geolocation is not supported')
    }
  }

  initGeolocation = () => {
    if (!('geolocation' in window.navigator)) {
      console.error('geolocation is not supported')
      return false
    }

    window.navigator.geolocation.getCurrentPosition(
      (pos) => {
        this.setGeolocation(
          {
            pos: {
              lat:          pos.coords.latitude,
              lng:          pos.coords.longitude,
              pos_accuracy: pos.coords.accuracy
            },
            alt: {
              alt:          pos.coords.altitude,
              alt_accuracy: pos.coords.altitudeAccuracy
            }
          }
        )
      },
      (response) => {
        console.error('geolocation error')
      },
      {enableHighAccuracy:true}
    )
  }

  moveGeolocation = (event) => {
    const { geolocation } = this.state
    const old_position = geolocation.pos
    const gpsFilterEnable = true
    const GPS_THRESHOLD = 65
    const FILTER_COEFFICIENT = 1.0

    const { latitude, longitude, accuracy, altitude, altitudeAccuracy } = event.coords
    let pos = {
      lat: latitude,
      lng: longitude,
      pos_accuracy: accuracy
    }
    const alt = {
      alt:          altitude,
      alt_accuracy: altitudeAccuracy
    }

    if ( 'isDefault' in geolocation ) {
      this.setGeolocation({ pos, alt })
      return true
    }

    if (gpsFilterEnable && pos.pos_accuracy > GPS_THRESHOLD) {
      let diff_lat = old_position.lat - pos.lat
      let diff_lng = old_position.lng - pos.lng
      pos.lat = old_position.lat + diff_lat / (pos.pos_accuracy - GPS_THRESHOLD) * FILTER_COEFFICIENT
      pos.lng = old_position.lng + diff_lng / (pos.pos_accuracy - GPS_THRESHOLD) * FILTER_COEFFICIENT
    }
    this.setGeolocation({ pos, alt })
  }

  setGeolocation = (geolocation) => {
    this.setState({geolocation})
    this.props.callbackGeolocation(geolocation)
  }

  render () {
    return null
  }
}

Geolocation.propTypes = {
  callbackGeolocation: PropTypes.func.isRequired,
  useGeolocation: PropTypes.bool.isRequired,
  startPosition: PropTypes.object.isRequired,
};

export default Geolocation
