import _ from 'lodash'
import React, { Component } from 'react';

import SoundInfoModal from './soundInfo-modal';

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from '@material-ui/core/ListItemIcon';
import LockIcon from '@material-ui/icons/Lock';
import Grid from '@material-ui/core/Grid';
import {PAUSE, PLAYING} from '../types/sounds';

import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
// import '../assets/styles/sound-controller.css'


class SoundListPanel extends Component {
  state = {
    isModalSmall :false,
  }
  componentDidMount(){
    const screenWidth=window.parent.screen.width;

    if(screenWidth<=320){
      this.setState({isModalSmall: true});
    }else{
      this.setState({isModalSmall: false});
    }
  }

  shouldComponentUpdate(nextProps, prevState) {
    if((nextProps.soundInfo !== this.props.soundInfo)||(nextProps.soundState !== this.props.soundState)){
      return true
    }
    return false
  }

  getIsSelected = (soundId) => {
    return this.props.soundInfo && (soundId === this.props.soundInfo.key);
  }

  soundListRender () {
    const listStates = {
      locked:   "playerListItem listItem-locked",
      ready:    "playerListItem listItem-ready",
      playing:  "playerListItem listItem-playing",
      paused:   "playerListItem listItem-paused",
      bonus:    "playerListItem listItem-bonus",
      blink:    "playerListItem listItem-blink"
    }
    const { soundState, soundList} = this.props;
    const onStyle = { fontSize: "20" ,color: "white"};
    const offStyle = { fontSize: "20" ,color: "gray"};
    let arr = _.map(soundList, (soundInfo, key) => {
      
      let infoIcon = <SoundInfoModal  style={(soundInfo.unlocked) ? onStyle : offStyle} soundInfo={soundInfo} isSmall={this.state.isModalSmall}/>
      let stateIcon = <LockIcon style={offStyle} />
      let listState = listStates.locked;
      let name = soundInfo.name;

      const isThisSelected = this.getIsSelected(key);
      const isThisPlaying = this.getIsSelected(key) && (soundState === PLAYING);
      // console.log(isThisPlaying, isThisSelected)

      if(soundInfo.unlocked){
        if(isThisPlaying){
          stateIcon = <PauseIcon style={onStyle} />
          listState = listStates.playing;
          this.isPaused = false;
        }else{
          stateIcon = <PlayArrowIcon style={onStyle} />
          if(isThisSelected){
            listState = listStates.playing;
            // listState = listStates.paused;
            this.isPaused = true;
          }else{
            listState = listStates.ready;
            if(this.props.blinkList){
              this.props.blinkList.forEach(blinkId => {
                if (blinkId === key) {
                  listState = listStates.blink
                }
              })
            }
          }
        }
      }else{
        if(soundInfo.isBonus){
          listState = listStates.locked;
          name = "？？？？？？？？";
        }
      }
      return (
        <ListItem className={listState} style={this.isPaused ? {animationPlayState:  'paused'}: {}} key={key} order={soundInfo.order}>
          <Grid container spacing={24}>
            <Grid item xs={2} onClick={() => {this.onListClicked(soundInfo, isThisPlaying)}}>
              <ListItemIcon>{stateIcon}</ListItemIcon>
            </Grid>
            <Grid item xs={8} onClick={() => {this.onListClicked(soundInfo, isThisPlaying)}}>
              {name}
            </Grid>
            <Grid item xs={2} >
              <ListItemIcon>{infoIcon}</ListItemIcon>
            </Grid>
          </Grid>
        </ListItem>
      )
    });
    return _.sortBy(arr, ['props.order'])
  }

  onListClicked = (soundInfo, isPlaying) => {
    if(soundInfo.unlocked){
      if(isPlaying){
        this.props.selectMusic(soundInfo);
        this.props.setSoundState(PAUSE)
      }
      else{
        this.props.selectMusic(soundInfo);
        this.props.setSoundState(PLAYING)
      }
    }
  }

  onInfoClicked = () => {
  }

  render(){
    return(
      <div className="list">
      <List>
        {this.soundListRender()}
      </List>
    </div>
    )
  }
}export default(SoundListPanel)
