import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MediaQuery from 'react-responsive';

class Countdown extends Component{
  constructor(props) {
    super(props);

    this.state = {
      days: 0,
      hours: 0,
      min: 0,
      sec: 0,
    }
  }

  componentDidMount() {
    // update every second
    this.interval = setInterval(() => {
      const date = this.calculateCountdown(this.props.date);
      date ? this.setState(date) : this.stop();
    }, 1000);
  }

  componentWillUnmount() {
    this.stop();
  }

  calculateCountdown(endDate) {
    let diff = (Date.parse(new Date(endDate)) - Date.parse(new Date())) / 1000;

    // clear countdown when date is reached
    if (diff <= 0) return false;

    const timeLeft = {
      years: 0,
      days: 0,
      hours: 0,
      min: 0,
      sec: 0
    };

    // calculate time difference between now and expected date
    if (diff >= (365.25 * 86400)) { // 365.25 * 24 * 60 * 60
      timeLeft.years = Math.floor(diff / (365.25 * 86400));
      diff -= timeLeft.years * 365.25 * 86400;
    }
    if (diff >= 86400) { // 24 * 60 * 60
      timeLeft.days = Math.floor(diff / 86400);
      diff -= timeLeft.days * 86400;
    }
    if (diff >= 3600) { // 60 * 60
      timeLeft.hours = Math.floor(diff / 3600);
      diff -= timeLeft.hours * 3600;
    }
    if (diff >= 60) {
      timeLeft.min = Math.floor(diff / 60);
      diff -= timeLeft.min * 60;
    }
    timeLeft.sec = diff;

    return timeLeft;
  }

  stop() {
    clearInterval(this.interval);
  }

  addLeadingZeros(value) {
    value = String(value);
    while (value.length < 2) {
      value = '0' + value;
    }
    return value;
  }

  render() {
    const countDown = this.state;

    return (

      <div className="Countdown">

      <MediaQuery orientation="landscape">
        <span className="Countdown-col">
          <span className="Countdown-col-element">
              <span className="cd_number_ls glowWhite">0</span>
              <span className="cd_metrics_ls">{countDown.days === 1 ? 'DAY' : 'DAYS'}</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number_ls glowWhite">{this.addLeadingZeros(countDown.hours)}</span>
            <span className="cd_metrics_ls">HOURS</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number_ls glowWhite">{this.addLeadingZeros(countDown.min)}</span>
            <span className="cd_metrics_ls">MINUTES</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number_ls glowWhite">{this.addLeadingZeros(countDown.sec)}</span>
            <span className="cd_metrics_ls">SECONDS</span>
          </span>
        </span>
      </MediaQuery>

      <MediaQuery orientation="portrait">
        <span className="Countdown-col">
          <span className="Countdown-col-element">
              <span className="cd_number glowWhite">0</span>
              <span className="cd_metrics">{countDown.days === 1 ? 'DAY' : 'DAYS'}</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number glowWhite">{this.addLeadingZeros(countDown.hours)}</span>
            <span className="cd_metrics">HOURS</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number glowWhite">{this.addLeadingZeros(countDown.min)}</span>
            <span className="cd_metrics">MINUTES</span>
          </span>
        </span>
        <span className="Countdown-col">
          <span className="Countdown-col-element">
            <span className="cd_number glowWhite">{this.addLeadingZeros(countDown.sec)}</span>
            <span className="cd_metrics">SECONDS</span>
          </span>
        </span>
      </MediaQuery>

      </div>
    );
  }
}

Countdown.propTypes = {
  date: PropTypes.string.isRequired
};

Countdown.defaultProps = {
  date: new Date()
};

export default Countdown;
