import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkInButtonImg from '../assets/images/check-in-button.png';

import _ from 'lodash';

class PreloadImage extends Component{

componentDidMount(){
  const areaImage = this.getImages(this.props.settingData.appSetting.areas);
  const couponImage = this.getImages(this.props.settingData.couponList);
  this.preloadImagesPath = areaImage.concat(couponImage);
}

getImages(obj){
  const imgArray =  _.map(obj, (item, key) =>{
    return item['image'];
  })
  return imgArray
}

generateJSX(){
  let imgJSX = [];
  for(let i in this.preloadImagesPath){
    let content = (
      <div key={i}>
        <img src={this.preloadImagesPath[i]} alt='preload' />
      </div>
    )
    imgJSX.push(content);
  }
  return imgJSX;
}

render(){
  return(
  <div　className='preloadImage' style={{display: 'none', zIndex:0}}>
    {this.generateJSX()}
    <img src={checkInButtonImg} alt='checkinbutton'/>
  </div>
  )
}

}export default connect(state => state)(PreloadImage)