import React, { Component } from 'react';
import { Redirect } from  'react-router-dom';
import { connect } from  'react-redux';
import MediaQuery from 'react-responsive';
import WindowUtils from '../utils/common/window-utils';
import GATracker from '../components/ga-tracker';
// styles
import '../assets/styles/share.css';
// images
import title from '../assets/images/app-title.png';
import urlTextSampleImg from '../assets/images/url-text-sample.png';
import browserSampleImg from '../assets/images/browser-sample.png';
import { BROWSER_CHROME } from '../types/browser.js'
import qrCodeImg from '../assets/images/QRCode.png';

class Share extends Component{
  onURLClick = (event) => {
    // const url = event.currentTarget.textContent;
    const url = "https://sound-rpg.com/";
    // This solusion doesn't work on iOS safari
    // navigator.clipboard.writeText(url).then(() => {
    //   alert(`${url}をクリップボードにコピーしました。`)
    // })

    WindowUtils.Clipboard.copy(url);
    alert(`${url}をクリップボードにコピーしました。`)
  }

  render() {
    if ((this.props.isIphone || this.props.isIpad) && (this.props.browserType === BROWSER_CHROME)) {
      return (
        <Redirect to="/" />
      )
    }
    return (
      <div className='shareContainer'>
        <GATracker pathname={this.props.location.pathname} />
        <div className='shareBackground'></div>
        <div style={{ marginTop: `${window.innerHeight * .5}px`, transform: 'translateY(-50%)' }}>
        {
          this.props.isMobile ? (
            <div>
              <MediaQuery orientation='portrait'>
                <center>
                  <img src={title}
                    className='shareTitle'
                    alt='title'
                  />
                  <hr style={{ borderTop: '.105vw solid white', marginTop: '-2.5vw', width: '90vw'}} />
                  <div className='shareCatchphrase'>
                    2019.2.4 <span className='day'>(MON)</span> 〜 2019.２.11 <span className='day'>(MON)</span><br />札幌エリア限定 / 期間限定
                  </div>
                  <hr style={{ borderBottom: '.105vw solid white', width: '90vw'}} />
                  <div className='shareDescription'>
                    SNSアプリからリンクを開いた方は、次のステップを行ってください
                    <div className='engInstruction' style={{marginBottom: '5vw'}}>
                      Who opened this link from an SNS app, follow the next steps
                    </div>
                    <div className='instruction'>
                      <div>
                        1. 下のボタンからURLをコピー
                        <div className='engInstruction' style={{ marginBottom: '-.5em' }}>&nbsp;&nbsp;&nbsp;Copy the URL below</div>
                      </div>
                      <div className='urlButton' onClick={this.onURLClick}>
                        <div className='urlText'>
                          ここをタップしてURLをコピーしてください。
                          <div style={{ color: '#cc4422', marginTop: '.1em' }}>https://sound-rpg.com</div>
                        </div>
                      </div>
                      <div style={{ marginTop: '7vw' }}>
                        2. SafariまたはChromeのを開く
                        <div className='engInstruction'>&nbsp;&nbsp;&nbsp;Open Safari or Chrome</div>
                        <div>
                          <img src={browserSampleImg}
                            className='deviceFrame'
                            alt='browser icon'
                          />
                        </div>
                      </div>
                      <div style={{marginTop: '5vw'}}>
                        3. URLを貼付けてアクセス
                        <div className='engInstruction'>&nbsp;&nbsp;&nbsp;Paste the URL and access</div>
                      </div>
                      <div>
                        <img src={urlTextSampleImg}
                          className='deviceFrame'
                          alt='url bar'
                        />
                      </div>
                    </div>
                  </div>
                </center>
              </MediaQuery>

              <MediaQuery orientation='landscape'>
                <div>
                  <img src={title}
                    className='shareTitleLs'
                    alt='title'
                  />
                  <hr style={{ width: '70vw', borderTop: '.105vw solid white'}}/>
                  <div className='shareCatchphraseLs'>
                    2019.2.4 <span className='day'>(MON)</span> 〜 2019.２.11 <span className='day'>(MON)</span>&nbsp;&nbsp;&nbsp;&nbsp;札幌エリア限定&nbsp;&nbsp;/&nbsp;&nbsp;期間限定
                  </div>
                  <hr style={{ width: '70vw', borderBottom: '.105vw solid white'}}/>
                </div>
              </MediaQuery>
            </div>
          ) : (
            <div align='center'>
              <img src={title}
                className='share_title_ls'
                alt='title'
                    style={{ pointerEvents: 'none', width: '30vw' }} />
              <hr style={{ width: '50vw', borderTop: '.105vw solid white' }} />
              <div className='share_textcatchphrase_ls' style={{fontSize: '1vw'}}>
                2019.2.4 <span className='day'>(MON)</span> 〜 2019.２.11 <span className='day'>(MON)</span>&nbsp;&nbsp;&nbsp;&nbsp;札幌エリア限定&nbsp;&nbsp;/&nbsp;&nbsp;期間限定
                  </div>
              <hr style={{ width: '50vw', borderBottom: '.105vw solid white', marginBottom: '1vw' }} />

              <table width="55%">
                <tbody>
                <tr>
                  <td align='center'>
                    <div className='shareDescriptionLs' style={{marginTop: '1vw',marginBottom: '2vw'}}>スマートフォンのSafariまたはChromeから<br></br>アクセスしてください。下のQRコードからでもURLを読み取れます。
                      <br />
                      <span className='engShareDescriptionLs'>
                      Please access using Safari or Chrome<br></br>from your smartphone. You can get the URL from the QR code below.
                      </span>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td align='center'>
                    <img src={qrCodeImg}
                        className='qrCode'
                        alt='qrcode'
                      />
                      <div style={{marginTop: '0.5vw'}}>
                      https://sound-rpg.com
                      </div>
                  </td>
                </tr>
                </tbody>
              </table>
              

            </div>
          )
        }
        </div>
      </div>
    )
  }
}

export default connect(state => state)(Share);
