// react
import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types';
import GATracker from '../components/ga-tracker';
import Teaser from '../components/teaser';
import Thanks from '../components/thanks';
// utils
import TimeUtility from '../utils/common/time-utility'

class EventPeriod extends Component {
  state = {
    eventStarted: false,
    eventEnded: false
  }

  componentDidMount() {
    const timeUtil = new TimeUtility()
    const { startDateTime, endDateTime } = this.props



    if (timeUtil.isAfterTheDate(startDateTime.year,
                                startDateTime.month,
                                startDateTime.day,
                                startDateTime.hour,
                                startDateTime.min
    ) || localStorage.getItem(this.LOCALSTARAGE_KEY)) {
      this.setState({
        eventStarted: true,
      });
    }

    if (timeUtil.isAfterTheDate(endDateTime.year,
                                endDateTime.month,
                                endDateTime.day,
                                endDateTime.hour,
                                endDateTime.min
    )) {
      this.setState({ eventEnded: true })
    }
  }

  render() {
    const { eventStarted, eventEnded } = this.state
    if (this.props.useTeaser && !eventStarted) {
      return (
        <Fragment>
          <GATracker pathname="/" />
          <Teaser />
        </Fragment>
      )
    }
    if (eventEnded) {
      return (
        <Fragment>
          <GATracker pathname="/" />
          <Thanks />
        </Fragment>
      )
    }
    return this.props.children
  }
}

EventPeriod.propTypes = {
  useTeaser: PropTypes.bool.isRequired,
  startDateTime: PropTypes.object.isRequired,
  endDateTime: PropTypes.object.isRequired,
};
export default EventPeriod
