import { SET_SENSOR_ON,
         SET_IS_IPHONE, SET_IS_IPAD, SET_ISMOBILE, SET_ISSUPPORTED,
         SET_COMPASSHEADING, SET_ROTATION, SET_DEVICEORIENTATION,
         INIT_GEOLOCATION, INIT_GEOLOCATION_OUTOF_SUPPORT, INIT_GEOLOCATION_FAILURE
} from '../types/sensor-datas'
import { SET_BROWSER_TYPE } from '../types/browser'

export const setSensorOn = () => {
  return{
    type: SET_SENSOR_ON,
    payload: true
  }
}

