export const initGeolocation = () => {
  if (!('geolocation' in window.navigator)) {
    console.error('geolocation is not supported')
    return{
      type: INIT_GEOLOCATION_OUTOF_SUPPORT,
      payload: false
    }
  }
  return dispatch => {
    window.navigator.geolocation.getCurrentPosition(
      (pos) => {
        dispatch({
          type: INIT_GEOLOCATION,
          payload: {
            pos: {
              lat:          pos.coords.latitude,
              lng:          pos.coords.longitude,
              pos_accuracy: pos.coords.accuracy
            },
            alt: {
              alt:          pos.coords.altitude,
              alt_accuracy: pos.coords.altitudeAccuracy
            }
          }
        })
      },
      (response) => {
        console.error('geolocation error')
        dispatch({
          type: INIT_GEOLOCATION_FAILURE,
          payload: false
        })
      },
      {enableHighAccuracy:true}
    )
  }
}

export const moveGeolocation = (event, old_position) => {
  let gpsFilterEnable = true
  let GPS_THRESHOLD = 65
  let FILTER_COEFFICIENT = 1.0
  let lat = event.coords.latitude
  let lng = event.coords.longitude
  let pos_accuracy = event.coords.accuracy

  let altitude = {
    alt:          event.coords.altitude,
    alt_accuracy: event.coords.altitudeAccuracy
  }

  if (gpsFilterEnable && pos_accuracy > GPS_THRESHOLD) {
    let diff_lat = old_position.lat - lat
    let diff_lng = old_position.lng - lng
    lat = old_position.lat + diff_lat / (pos_accuracy - GPS_THRESHOLD) * FILTER_COEFFICIENT
    lng = old_position.lng + diff_lng / (pos_accuracy - GPS_THRESHOLD) * FILTER_COEFFICIENT
  }

  let position = {
    lat,
    lng,
    pos_accuracy
  }

  return {
    type: INIT_GEOLOCATION,
    payload: {
      pos: position,
      alt: altitude
    }
  }
}

export const setGeolocation = (position) => {
  return {
    type: INIT_GEOLOCATION,
    payload: {
      pos: position,
      alt: {
        alt: 0,
        alt_accuracy: 0
      }
    }
  }
}

