import React, { Component } from 'react'
import ReactModal from 'react-modal'
import '../assets/styles/modal.css'

//import CancelIcon from '@material-ui/icons/CancelOutlined'
import CloseIcon from '@material-ui/icons/Cancel'

ReactModal.setAppElement('#root');

/*
   -- props you can pass --
   showModal:        to control modal-template from parent
   defaultShowModal: initial showModal status
   glow:             to change the design
   smallGlow:        to change the design for coupon
   shine:            to change the design
   noCloseButton:    to change the design
   noPadding:        to change the design
   callBackState:    function: to give back the new state.showModal
   callbackClose:    function: to give back if modal is closed
*/
class ModalTemplate extends Component{
  state = {
    showModal: false,
  }

  componentDidMount() {
    if (this.props.defaultShowModal) {
      this.setState({ showModal: this.props.defaultShowModal })
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { showModal } = nextProps
    const ret = {}
    if ((showModal !== undefined) && (showModal !== prevState.showModal)) {
      ret.showModal = showModal
    }
    return ret
  }

  handleOpenModal = () => {
    this.handleModal(true)
  }

  handleCloseModal = () => {
    this.handleModal(false)
    if (this.props.callbackClose) {
      this.props.callbackClose()
    }
  }

  handleModal = (showModal) => {
    this.setState({ showModal })
    this.callBackState(showModal)
  }

  callBackState = (showModal) => {
    if (this.props.callBackState) {
      this.props.callBackState(showModal)
    }
  }

  render () {
    return (
        <ReactModal
          isOpen={this.state.showModal}
          contentLabel="onRequestClose Example"
          onRequestClose={this.handleCloseModal}
        className={`${this.props.isShort ? "Modal-Short" : 'Modal-Long'} ${this.props.glow ? "glow" : ''} ${this.props.shine ? "shine" : ''} ${this.props.smallGlow ? "smallGlow" : ''} ${this.props.revealModal ? "revealModal" : ''}`}
          overlayClassName="Overlay"
          shouldFocusAfterRender={false}
          shouldCloseOnOverlayClick={this.props.shouldCloseOnOverlayClick === false ? false : true}
        >
          {/* If shouldCloseOnOverlayClick is undefined, this should set "true" */}
          { !this.props.noCloseButton && (
          <div className="uiArea">
            {/*<IconButton onClick={this.handleCloseModal}>*/}
            
              <CloseIcon className = "closeButton" style={{ fontSize: "30",color: "white"}} onClick={this.handleCloseModal}/>
            {/*</IconButton>*/}
          </div>
          )}
          <div className={`${this.props.noPadding ? '': 'content'}`}>
            {this.props.children}
          </div>
        </ReactModal>
    );
  }
}
export default ModalTemplate

