import _ from 'lodash'
import { INIT_SETTINGS, CHANGE_SOUND_LOCK, LOCK_ALL_SOUNDS, SET_SOUND_COMPLETE, CHANGE_COUPON_VISIBLE, CHANGE_COUPON_USED, SOUND_LIST_DATA_NAME, COUPON_LIST_DATA_NAME} from '../types/settings';

// const LOCAL_STORAGE_SOUND_LIST = 'soundList';
const LOCAL_STORAGE_SETTING_UPDATED_DATE = 'settingUpdatedDate';

const initState = null;
export default (state = initState, action) => {
  let soundList = {};
  let couponList = {};
  // const {soundId, unlocked} = action.payload;

  switch(action.type) {
    case INIT_SETTINGS:
      // soundList = JSON.parse(localStorage.getItem(LOCAL_STORAGE_SOUND_LIST))
      // if (!soundList) {
      //   soundList = setInitialSoundStates(action.payload.soundList)
      // }
      let lastUpdatedDate = localStorage.getItem(LOCAL_STORAGE_SETTING_UPDATED_DATE)
      if (!lastUpdatedDate) {
        lastUpdatedDate = action.payload.appSetting.lastModified
        localStorage.setItem(LOCAL_STORAGE_SETTING_UPDATED_DATE, lastUpdatedDate)
      }
      // const storageSoundList = JSON.parse(localStorage.getItem(SOUND_LIST_DATA_NAME))
      // if (!storageSoundList) {
      //   soundList = setInitialSoundStates(action.payload.soundList)
      // } else {
      //   if (isSettingUpdated(lastUpdatedDate, action.payload.appSetting.lastModified)) {
      //     localStorage.setItem(LOCAL_STORAGE_SETTING_UPDATED_DATE, action.payload.appSetting.lastModified)
      //     soundList = mergedDataList(storageSoundList, action.payload.soundList)
      //     localStorage.setItem(SOUND_LIST_DATA_NAME, JSON.stringify(soundList))
      //   } else {
      //     soundList = storageSoundList
      //   }
      // }

      soundList = dataListSetUp(lastUpdatedDate, action.payload, SOUND_LIST_DATA_NAME);
      couponList = dataListSetUp(lastUpdatedDate, action.payload, COUPON_LIST_DATA_NAME);
      return { ...action.payload, soundList, couponList }

    case CHANGE_SOUND_LOCK:
      const {soundId, unlocked} = action.payload;
      soundList = {...state.soundList, [soundId]: {...state.soundList[soundId], unlocked}}
      localStorage.setItem(SOUND_LIST_DATA_NAME, JSON.stringify(soundList))
      return { ...state, soundList }

    case SET_SOUND_COMPLETE:
      // Unlock all the bonus sounds
      soundList = _.cloneDeep(state.soundList)
      for (let key in soundList) {
        if (soundList[key].isBonus) {
          soundList[key].unlocked = true
        }
      }
      localStorage.setItem(SOUND_LIST_DATA_NAME, JSON.stringify(soundList))
      return { ...state, soundList }

    case LOCK_ALL_SOUNDS:
      soundList = setInitialDataStates(state.soundList, SOUND_LIST_DATA_NAME)
      localStorage.removeItem(SOUND_LIST_DATA_NAME)
      localStorage.removeItem(COUPON_LIST_DATA_NAME)
      localStorage.removeItem(LOCAL_STORAGE_SETTING_UPDATED_DATE)
      return { ...state, soundList }

    case CHANGE_COUPON_VISIBLE:
      const { couponId_visible, visible} = action.payload;
      couponList = {...state.couponList, [couponId_visible]: {...state.couponList[couponId_visible], visible}}
      localStorage.setItem(COUPON_LIST_DATA_NAME, JSON.stringify(couponList))
      return { ...state, couponList }

    case CHANGE_COUPON_USED:
      const { couponId_used, used} = action.payload;
      couponList = {...state.couponList, [couponId_used]: {...state.couponList[couponId_used], used}}
      localStorage.setItem(COUPON_LIST_DATA_NAME, JSON.stringify(couponList))
      return { ...state, couponList }

    default:
      return state
  }
}

function dataListSetUp(lastUpdatedDate, payload, DATALSIT_NAME){
  const storageDataList = JSON.parse(localStorage.getItem(DATALSIT_NAME))
  let dataList = null;
  if (!storageDataList) {
    dataList = setInitialDataStates(payload[DATALSIT_NAME], DATALSIT_NAME)
  } else {
    if (isSettingUpdated(lastUpdatedDate, payload.appSetting.lastModified)) {
      localStorage.setItem(LOCAL_STORAGE_SETTING_UPDATED_DATE, payload.appSetting.lastModified)
      dataList = mergedDataList(storageDataList, payload[DATALSIT_NAME], DATALSIT_NAME)
      localStorage.setItem(DATALSIT_NAME, JSON.stringify(dataList))
    } else {
      dataList = storageDataList
    }
  }
  return dataList
}

function setInitialSoundState(sound, key) {
  return { ...sound, unlocked: false, key }
}

function setInitialCouponState(coupon, key){
  return {...coupon, visible: false, used: false, key }
}

function setInitialDataStates(dataList, DATALSIT_NAME) {
  let temporary = null;
  if(DATALSIT_NAME === SOUND_LIST_DATA_NAME){
    temporary = _.map(dataList, setInitialSoundState);
  }else if(DATALSIT_NAME === COUPON_LIST_DATA_NAME)
  {
    temporary = _.map(dataList, setInitialCouponState);
  }
  return _.keyBy(temporary, 'key')
}

function isSettingUpdated(storage, loaded) {
  const storageDate = new Date(storage)
  const loadedDate = new Date(loaded)
  return storageDate < loadedDate
}
function mergedDataList(storageData, loadedData, DATALSIT_NAME) {
  for (let keyL in loadedData) {
    let newKey = true
    for (let key in storageData) {
      if (key !== keyL) continue
      newKey = false
      for (let keyInSound in loadedData[key]) {
        // Update the same key infomation
        storageData[key][keyInSound] = loadedData[key][keyInSound]
      }
    }
    if (newKey) {
      if(DATALSIT_NAME === SOUND_LIST_DATA_NAME){
        storageData[keyL] = setInitialSoundState(loadedData[keyL], keyL)
      }else if(DATALSIT_NAME === COUPON_LIST_DATA_NAME){
        storageData[keyL] = setInitialCouponState(loadedData[keyL], keyL)
      }
    }
  }
  return storageData
}
