import React, { Component } from 'react';
import ModalTemplate from './modal-template';
import { connect } from 'react-redux';
import $ from 'jquery';
//aciton
import {setPresentObtain} from '../actions/settings';
//assets
import usedIcon from '../assets/images/usedIcon.png';
import EntireMap from '../assets/images/map-gift.png';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
// import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

class PresentModal extends Component {
  state = {
    dialogOpen: false,
  }

  shouldComponentUpdate(nextProps, prevState) {
    if(nextProps.showModal !== this.props.showModal){
      return true
    }
    if(this.state.dialogOpen !== prevState.dialogOpen){
      return true
    }
    return false
  }

  style = {height: `${window.innerHeight - (this.props.isSmall ? 175 : 190)}px`}

  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  handleDialogOpen = () => {
    this.setState({
      dialogOpen: true,
    });
  };

  handleDialogCloseAgree = () => {
    this.props.setPresentObtain();
    this.setState({
      dialogOpen: false,
    });
  };

  handleDialogCloseDisagree = () => {
    this.setState({
      dialogOpen: false,
    });
  };

  dialogRender(){
    return(
      <Dialog
      open={this.state.dialogOpen}
      onClose={this.handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      style={{zIndex:'9999'}}
    >
      <DialogTitle id="alert-dialog-title">{"プレゼントを取得済みにしますか？"}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          この操作は取り消しできません
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={this.handleDialogCloseDisagree} color="primary">
          キャンセル
        </Button>
        <Button onClick={this.handleDialogCloseAgree} color="primary">
          OK
        </Button>
      </DialogActions>
    </Dialog>
    )
  }

  usedIconRender(){
    if(this.props.presentObtain){
      return (

        <img src={usedIcon} alt="usedIcon" style={{position:"absolute"}} />
      )
    }
    return null
  }

  presentCardRender(){
    if(!this.props.soundComplete){
      return (
        <div style={{marginTop:"10px",marginBottom:"10px"}}>
         <div>５箇所のスポットをすべて巡ると...<br></br><br></br>さっぽろ雪まつり大通り会場５丁目東「道新雪の広場」にある「道新かなでるミライステーション」でプレゼントを手に入れよう!
         <br/>プレゼント交換可能時間：10:00 〜 20:00</div>
         <br></br>
         <div className='modal-content-en'>After visiting all 5 spots...<br></br><br></br>Get your gift at "Doshin Yuki No Hiroba" at Sapporo snow festival Odori 5-chome east! Booth name: "Doshin Kanaderu Mirai Station". Opens from 10:00 to 20:00</div>
        </div>
      )
    }
    return (
      <div>
        <div style={{marginLeft:"30px",marginRight:"30px",textAlign:"center" }}>
        {this.dialogRender()}
        </div>
        <div style={{marginTop:"30px",marginBottom:"30px"}}>
          プレゼントに交換できます！
          <br></br><br></br>
          さっぽろ雪まつり大通り会場５丁目東「道新雪の広場」にある「道新かなでるミライステーション」でプレゼントを手に入れよう!
          <br/>
          プレゼント交換可能時間：10:00 〜 20:00
          <div className='modal-content-en'  style={{marginTop:"10px",marginBottom:"10px"}}>
            Now you can get the gift!
            <br></br><br></br>
            Get your gift at "Doshin Yuki No Hiroba" at Sapporo snow festival Odori 5-chome east! Booth name: "Doshin Kanaderu Mirai Station". Opens from 10:00 to 20:00
          </div>
          <Grid container justify="center">
              <Button variant="contained" onClick={this.handleDialogOpen} disabled={this.props.presentObtain} style={{background:"#e0e0e0"}}>
                <div>プレゼントを取得済みにする</div>
              </Button>
            </Grid>
        </div>
      </div>
    )
  }


  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }

    return (
      <div>
        {/* {console.log('preset')}  */}
        <ModalTemplate showModal={this.props.showModal}
          callBackState={this.handleModal}
          shouldCloseOnOverlayClick={true}
          isShort={false}
          isSmall={this.props.isSmall}
          revealModal
          >
          <div className="modal-miniTitle" onClick={this.scrollTop}>
            プレゼント<br />
            Gift
          </div>
          <div className={`modal-content scroll-modal`} style={style}>
            {this.presentCardRender()}
            <div>
                <img src={EntireMap} alt="map" width="100%" />
            </div>
          </div>
        </ModalTemplate>
      </div>
    )
  }
}
export default connect(state => state, {setPresentObtain})(PresentModal)
