import React, { Component } from 'react'
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import Figure from '../assets/images/firstTimeGuide.png'

const theme = createMuiTheme({
  overrides: {
    // Name of the component ⚛️ / style sheet
    MuiBackdrop: {
      // Name of the rule
      root: {
        // Some CSS
        backgroundColor: 'rgba(255,0,0,0)',
      },
    },
  },
  typography: {
    useNextVariants: true,
  },
});


class FirstTimeGuideDialog extends Component {
  state = {
    dialogOpen: true,
    showGuide: false,
  }

  componentDidMount() {
    const STORAGE_DID_READ_MAP_GUIDE = 'STORAGE_DID_READ_MAP_GUIDE';
    const didReadMapGuide = localStorage.getItem(STORAGE_DID_READ_MAP_GUIDE);
    if (!didReadMapGuide && this.props.inEventArea) {
      localStorage.setItem(STORAGE_DID_READ_MAP_GUIDE, true);
      this.setState({showGuide: true});
    }
  }

  handleClose = () => {
    this.setState({
      dialogOpen: false
    });
  };
  onClose = () => {
  }

  render () {
    if (!this.props.inEventArea || !this.state.showGuide) {
      return null
    }

    return (
      <MuiThemeProvider theme={theme}>
        <Dialog
          open={this.state.dialogOpen}
          onClose={this.handleClose}
          onBackdropClick={this.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
          style={{zIndex:'9999',textAlign:'center'}}
          PaperProps={{
            style: {
              backgroundColor: 'black',
              boxShadow: '0 0 15px #FFFFFF'
            },
          }}
        >
          <DialogTitle id="alert-dialog-title" style={{color:'white'}}>
          <div style={{color:'white'}}>
            音楽を探しに行こう！
          </div>
          </DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description" style={{color:'#DDDDDD', fontSize:'.8em'}}>
              周りをよく見て、<br/>常に注意しながら歩いてください。<br />
            </DialogContentText>
            <DialogContentText id="alert-dialog-description" style={{color:'#DDDDDD', fontSize:'.6em'}}>
              <br />プライベートモード・<br />シークレットタブでの利用は、<br />動作を保証できないため、<br />お控えください。
            </DialogContentText>
          </DialogContent>
          <div align='center'>
            <img src={Figure} alt="FirstTimeGuideFigure" width="96%" style={{ pointerEvents: 'none' }} />
          </div>
          <DialogActions style={{justifyContent: 'center'}}>
            <Button variant="outlined" onClick={this.handleClose} 
              style= {
                {
                  width: '60px',
                  border: 'solid 1px #aaaaaa',
                  backgroundColor:'#000000',
                  color:'#FFFFFF'
                }}
            >
            OK
            </Button>
          </DialogActions>
        </Dialog>
      </MuiThemeProvider>
    )
  }
}
FirstTimeGuideDialog.propTypes = {
  inEventArea: PropTypes.bool.isRequired,
};
export default FirstTimeGuideDialog

