import _ from 'lodash'
import React, { Component } from 'react';
import { connect } from 'react-redux';
import SoundPlayer from '../containers/sound-player';
import SoundListPanel from './sound-list-panel';
import CheckBrowserInactive from '../utils/common/check-browser-inactive';

//actions
import { setSoundControllerOpenState } from '../actions/sounds';
import { requestSendLog } from '../actions/send-log-api'
import { SOUND_REACTION } from '../types/logs'

// material-ui components
import IconButton from '@material-ui/core/IconButton';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import RepeatIcon from '@material-ui/icons/Repeat';
import RepeatOneIcon from '@material-ui/icons/RepeatOne';
// material-ui components for drawer
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
// styles
import '../assets/styles/sound-controller.css'
import { withStyles } from '@material-ui/core/styles';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
//types
import {EMPTY, READY ,PAUSE, PLAYING} from '../types/sounds';
//jingle
import {Howl} from 'howler';


const StyledDrawer = withStyles({
  paper: {
    backgroundColor: 'rgba(0,0,0,0.7)',
    position: 'fixed',
    marginBottom : 0,
    zIndex: 1300,
  },
  root: {
    backgroundColor: 'rgba(0,0,255,0)',
    position: 'fixed',
    marginTop: 60,
    zIndex: 1300,
  }
})(SwipeableDrawer);

const theme = createMuiTheme({
  overrides: {
    // Name of the component ⚛️ / style sheet
    MuiBackdrop: {
      // Name of the rule
      root: {
        // Some CSS
        backgroundColor: 'rgba(0,255,0,0)',

        //this is needed to make dummyForTouch touchable
        position: 'fixed',
        marginTop: 60,
        zIndex: 1300,
      },
    },
  },
  typography: {
    useNextVariants: true,
  },
});

class SoundController extends Component {
  state = {
    soundInfo: null,
    soundState: EMPTY,
    isLoopOne: false,
    soundProgress: 0,
    isSmall: false,
    cutIn:{isRewinding: false, isJinglePlay: false}
  }

  // static getDerivedStateFromProps(nextProps, prevState) {
  //   const { soundInfo } = nextProps;
  //   const ret = {};
  // 
  //   if ((soundInfo !== undefined) && (!prevState.soundInfo || (soundInfo.key !== prevState.soundInfo.key))) {
  //     ret.soundInfo = soundInfo;
  //   }
  //   return ret
  // }

  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    if ((this.props.currentArea) && (this.props.currentArea !== prevProps.currentArea)) {
      const soundId = this.props.settingData.appSetting.areas[this.props.currentArea].soundId;
      if(!this.props.soundList[soundId].unlocked){
        this.callPlayJingle();
      }
    }
    return null
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    //getSnapshotBeforeUpdate needs this function
  }

  componentDidMount(){
    //check screen size
    const screenWidth=window.parent.screen.width;

    if(screenWidth<=320){
      this.setState({isSmall: true});
    }else{
      this.setState({isSmall: false});
    }
    this.generateJingle();
    this.timeStock = 0;
    const isIOS = /iP(hone|(o|a)d)/.test(navigator.userAgent);
    // console.log(isIOS);
    if(isIOS){
      this.checkBrowserInactive = new CheckBrowserInactive();
      this.checkBrowserInactive.startObserve(this.forceBrowserReload);
    }
  }

  forceBrowserReload = () => {
    if(this.state.soundState === PAUSE){
      this.requestSendLog();
      window.location.reload();
    }
  }

  requestSendLog(){
    let listeningTime = this.timeStock + this.state.soundProgress;
    // console.log('listening time:' + listeningTime);

    // API Call: Sound Reaction End Song
    const params = { sound_id: this.state.soundInfo.key, action: 'sound_end', sound_progress: listeningTime }
    this.props.requestSendLog(SOUND_REACTION, params)
  }

  componentWillUnmount(){
    if(this.checkBrowserInactive){
      this.checkBrowserInactive.stopObserve();
      this.checkBrowserInactive = null;
    }
  }

  generateJingle(){
    const jingleOption = {
      src: '/assets/sounds/jingle.mp3',
      format: 'mp3',
      loop: false,
      html5:false,
      preload:true,
      volume: 0.7
      }
    this.jingle = new Howl(jingleOption);
  }

  callPlayJingle(){
    if(this.state.soundState === PLAYING){
      this.setJinglePlay(true);
    }else{
      this.jingle.play();
    }
  }

  jingleCallbackPlay = () => {
    const JINGLE_INTERVAL = 2000;
    if(this.state.cutIn.isJinglePlay && this.jingle){
      this.jingle.play();
      window.setTimeout(() => {this.setJinglePlay(false)}, JINGLE_INTERVAL);
    }
  }

  toggleDrawer = () => {
    this.setOpenState(!this.props.soundControllerOpenState)
  }
  openDrawer = () => {
    this.setOpenState(true)
  }
  closeDrawer = () => {
    this.setOpenState(false);
  }

  setOpenState = (openState) => {
    this.props.setSoundControllerOpenState(openState)
  }


  setRewinding = (isRewinding) => {
    this.setStateCutIn(isRewinding, this.state.isJinglePlay)
  }
  setJinglePlay = (isJinglePlay) => {
    this.setStateCutIn(this.state.isRewinding, isJinglePlay)
  }
  setStateCutIn = (isRewinding, isJinglePlay) => {
    this.setState({ cutIn: {isRewinding, isJinglePlay} });
  }
  setSoundState = (soundState) =>{
    this.setState({soundState});
  }


  play = () => {
    this.setSoundState(PLAYING)
  }
  pouse = () => {
    this.setSoundState(PAUSE)
  }
  stop = () => {
    this.setSoundState(READY)
  }
  togglePlay = () => {
    if(this.state.soundState === PLAYING) {
      this.pouse()
    } else {
      this.play()
    }
  }

  toggleLoop = () => {
    this.setState({isLoopOne: !this.state.isLoopOne});
  }

  setSoundProgress = (time) => {
    this.setState({soundProgress:time});
  }


  startRewind = () => {
    this.setRewinding(true)
  }
  finishRewind = () => {
    this.setRewinding(false)
    this.setState({ soundProgress: 0 });
  }

  onSoundEnd = () =>{
    if(!this.state.isLoopOne){
      this.requestSendLog()
      this.playNext();
    }else{
      this.timeStock += this.state.soundProgress;
    }
  }

  playNext = () => {
    this.playSkip(true);
  }

  playPrevious = () => {
    const SOUND_CHANGE_THRESHOLD = 3;
    if(this.state.soundProgress < SOUND_CHANGE_THRESHOLD){
      this.playSkip(false);
    }else{
      this.timeStock += this.state.soundProgress;
      this.startRewind()
    }
  }

  playSkip = (direction) => {
    //seek next song
    let songIndex = 0;
    let unlockedSongs = _.filter(this.props.soundList, 'unlocked');
    unlockedSongs = _.sortBy(unlockedSongs, ['order']);
    for(let i in unlockedSongs){
      if(this.state.soundInfo.key === unlockedSongs[i].key){
        songIndex = i;
      }
    }
    if(unlockedSongs.length <= 1) {
      this.startRewind()
      return
    };
    if(direction){
      if(songIndex < unlockedSongs.length - 1){
        ++songIndex;
      }else{
        songIndex = 0;
      }
    }else{
      if(songIndex > 0){
        --songIndex;
      }else{
        songIndex = unlockedSongs.length - 1;
      }
    }
    //play next song
    this.requestSendLog();
    this.selectMusic(unlockedSongs[songIndex]);
  }

  selectMusic = (soundInfo) => {
    this.setState({ soundInfo })
    // this.callbackSoundInfo(soundInfo)
  }

  // callbackSoundInfo = (soundInfo) => {
  //   if (this.props.callbackSoundInfo) {
  //     this.props.callbackSoundInfo(soundInfo)
  //   }
  // }

  playerPanelRender= () => {
    const {soundInfo} = this.state;
    let style = { fontSize: "40" ,color: "white"};

    if(!soundInfo){
      return (
        <div onClick={this.toggleDrawer} style={{'textAlign':'center','verticalAlign':'middle'}}>
        曲を選んでください
        <br />
        <span style={{fontSize: '.7em'}}>Select music to play</span>
        </div>
        )
    }

    const elapsedTime = Math.round(this.state.soundProgress);
    let elapsedTimeString = null;
    if(elapsedTime === -1){
      elapsedTimeString = 'Now Loading...'
    }else{
      const min = Math.floor(elapsedTime/60);
      const sec = elapsedTime % 60;
      elapsedTimeString = (sec > 9) ? min +  ":" + sec : min + ":0" + sec;
    }
    if(this.props.soundControllerOpenState){
      return this.unFoldedPlayer(elapsedTimeString, soundInfo, style);
    }else{
      return this.foldedPlayer(elapsedTimeString, soundInfo, style);
    }
  }

  foldedPlayer = (_elapsedTime, _soundInfo, _style) =>{
    const playStopIcon = (this.state.soundState === PLAYING) ? <PauseIcon style={_style} /> : <PlayArrowIcon style={_style} />
    return (
      <div>
        <div style={{'width':'55%','display':'inline-block','verticalAlign':'middle'}}>
          <div >{_soundInfo.name}  </div>
          <div className="elapsedTime">{_elapsedTime}</div>
        </div>
        <div style={{'width':'13%','display':'inline-block','verticalAlign':'middle', 'textAlign':'middle'}}>
        <IconButton onClick = {() => {this.playPrevious()}}>
          <SkipPreviousIcon style={_style}/>
        </IconButton>
        </div>
        <div style={{'width':'13%','display':'inline-block','verticalAlign':'middle', 'textAlign':'midlle'}}>
          <div>
            <IconButton onClick={() => { if (_soundInfo.unlocked) {this.togglePlay()}}}>
              {playStopIcon}
            </IconButton>
          </div>
        </div>
        <div style={{'width':'13%','display':'inline-block','verticalAlign':'middle', 'textAlign':'middle'}}>
        <IconButton onClick = {() => {this.playNext()}}>
          <SkipNextIcon style={_style}/>
        </IconButton>
        </div>
      </div>
    )
  }

  unFoldedPlayer = (_elapsedTime, _soundInfo, _style) => {
    const loopIcon = this.state.isLoopOne ? <RepeatOneIcon style={_style} /> : <RepeatIcon style={_style} />
    return (
      <div>
        <div style={{'width':'79%','display':'inline-block','verticalAlign':'middle'}}>
          <div >{_soundInfo.name}  </div>
          <div className="elapsedTime">{_elapsedTime}</div>
        </div>
        <div style = {{'width':'21%','display':'inline-block','verticalAlign':'middle','textAlign':'left'}}>
        <IconButton onClick = {() => {this.toggleLoop()}}>
          {loopIcon}
        </IconButton>
        </div>
      </div>
    )
  }

  controllTabRender () {
    const style = { fontSize: "40" }
    const icon = this.props.soundControllerOpenState ? <ExpandMoreIcon style={style} /> : <ExpandLessIcon style={style} />
    return (
      <div className="controllerTabArea">
        <div align="center" onClick={this.toggleDrawer}>
          { icon }
        </div>
          <div className="playerPanel">
            {this.playerPanelRender()}
          </div>
      </div>
    )
  }

  getIsSelected = (soundId) => {
    return this.state.soundInfo && (soundId === this.state.soundInfo.key);
  }

  // These are required by SwipableDrawer
  onClose = (event) => {
    // NOTE: closeDrawer() is required, coz SwipableDrawer closes drawer without changing the store data.
    this.closeDrawer();
  }
  onOpen = (event) => {}

  render() {
    const { soundControllerOpenState } = this.props;
    const soundPlayers = _.map(this.props.soundList, (soundInfo, key) => {
      // if (!soundInfo.unlocked || !this.getIsSelected(key)) return null
      if (!soundInfo.unlocked) return null
      return <SoundPlayer 
                          key={key}
                          soundInfo={soundInfo}
                          selectedSound={this.state.soundInfo}
                          soundState={this.state.soundState} 
                          onEnd={this.onSoundEnd} 
                          cutIn={this.state.cutIn} 
                          jingleCallbackPlay={this.jingleCallbackPlay}
                          finishRewind={this.finishRewind}
                          setSoundProgress={this.setSoundProgress} 
              />
    })
    return (
      <div>
         {/* You have to put soundPlayers here. if in Drawer, soundPlayers also be unmounted */}
         {soundPlayers}

        {!soundControllerOpenState && (
          <div className="controllerTabSmall"> {this.controllTabRender()} </div>
        )}

        {/*
        {soundControllerOpenState && (
          <div className="dummyForTouch" onClick = {this.closeDrawer}></div>
        )}
        */}

        <MuiThemeProvider theme={theme}>
          <StyledDrawer
            anchor="bottom"
            open={soundControllerOpenState}
            onClose={this.onClose}
            onOpen={this.onOpen}
          >
            <div className="controllerTabDrawer">
              {this.controllTabRender()}
              <SoundListPanel 
                              blinkList={this.props.blinkList}
                              soundInfo={this.state.soundInfo}
                              soundState={this.state.soundState}
                              soundList={this.props.soundList}
                              selectMusic={this.selectMusic}
                              setSoundState={this.setSoundState}
                            />
            </div>
          </StyledDrawer>
        </MuiThemeProvider>
      </div>
    )
  }
}
export default connect(state => state, { setSoundControllerOpenState, requestSendLog})(SoundController)

