// import _ from 'lodash'
import { Component } from 'react'
import PropTypes from 'prop-types';

class SensorDataHandler extends Component {
  componentDidMount(){
    // Initialize geolocation
    // if (this.props.settingData.appSetting.useGeolocation) {
    //   this.props.initGeolocation()
    //   this.initWatchPosition()
    // } else {
    //   this.props.setGeolocation(this.props.settingData.appSetting.startPosition)
    // }
    // Initialize mobile sensors
    this.platformInfo = window.navigator.userAgent.toLowerCase()
    this.setSensorHandlers()
  }

  componentWillUnmount() {
    window.removeEventListener('deviceorientationabsolute', this.deviceorientationabsolute, false)
    window.removeEventListener('deviceorientation', this.deviceorientation, false)
    window.removeEventListener('orientationchange', this.orientationchange, false)
  }

  // initWatchPosition = () => {
  //   if ('geolocation' in window.navigator){
  //     window.navigator.geolocation.watchPosition(
  //       (event) => this.props.moveGeolocation(event, this.props.geolocation.pos),
  //       null,
  //       {enableHighAccuracy:true, maximumAge: 3000, timeout: 30000}
  //     )
  //   } else {
  //     console.error('geolocation is not supported')
  //   }
  // }

  deviceorientationabsolute = (event) => {
    this.setCompassHeading(-event.alpha)
  }
  deviceorientation = (event) => {
    this.setCompassHeading(event.webkitCompassHeading)
  }
  orientationchange = (event) => {
    this.setOrientation()
  }
  setOrientation = () => {
    // this.props.setDeviceOrientation(window.orientation)
    this.props.callbackDeviceOrientation(window.orientation)
  }
  setSensorHandlers = () => {
    if(!this.props.isSupported) {
      return false
    }
    if ('ondeviceorientationabsolute' in window) {
      // for Android && chrome
      window.addEventListener('deviceorientationabsolute',this.deviceorientationabsolute, false)
    } else {
      //for iOS && safari.
      window.addEventListener('deviceorientation', this.deviceorientation, false)
    }
    // window.addEventListener('deviceorientation', (event) => {
    //   this.props.setRotation([event.alpha, event.beta, event.gamma])
    // }, false)
    this.setOrientation()
    window.addEventListener('orientationchange', this.orientationchange, false)
  }

  setCompassHeading = (compassHeading) => {
    // this.props.setCompassHeading(compassHeading)
    this.props.callbackCompassHeading(compassHeading)
  }

  render () {
    return null
  }
}

SensorDataHandler.propTypes = {
  callbackCompassHeading: PropTypes.func.isRequired,
  callbackDeviceOrientation: PropTypes.func.isRequired,
  isSupported: PropTypes.bool.isRequired,
};

export default SensorDataHandler
