//This class work on iOS7.1.2~9.3.2 / Android4.0.3~6.0.1

class CheckBrowserInactive {
  constructor(){
    this.hidden = null;
    this.visibilityChange = null;
    this.availableMethod = this.decideHowToCheck();
    this.callbackFunc = () => {return null}
  }

  startObserve(callbackFunc){
    this.callbackFunc = callbackFunc;
    if(this.availableMethod === 'onpagehide'){
      window.onpagehide = this.onBrowserVisibilityChange;
    } else if(this.availableMethod === 'blur'){
      window.addEventListener('blur', this.onBrowserVisibilityChange, false);
    } else {
      // this.observeWithRAF();
      if(this.availableMethod === 'visibilityChange'){
        document.addEventListener(this.visibilityChange, this.onBrowserVisibilityChange, false);
        // window.addEventListener('blur', this.onBrowserVisibilityChange, false);
      }
    }
    // console.log(this.visibilityChange)
    // console.log('start observe with ' + this.availableMethod);
  }

  decideHowToCheck(){
    let hidden, available;
    if (typeof document.hidden !== "undefined") {
      hidden = "hidden";
      this.visibilityChange = "visibilitychange";
    } else if (typeof document.webkitHidden !== "undefined") {
      hidden = "webkitHidden";
      this.visibilityChange = "webkitvisibilitychange";
    }

    if(window.navigator.userAgent.toLowerCase().indexOf('iphone os 7_0') > -1){
      available = 'onpagehide';
    } else if(typeof document[hidden] === "undefined"){
      if(window.requestAnimationFrame){
        available = 'requestAnimationFrame';
      } else {
        available = 'blur';
      }
    } else {
      // available = 'visibilityChange';
      available = 'blur';
    }
    this.hidden = hidden;
    return available
  }

  observeWithRAF(){
    let raf_timer = true;
    this.collbackFromRAF = () => {
      this.raf_id = window.requestAnimationFrame(this.collbackFromRAF);
      if(raf_timer !== false){
        clearTimeout(raf_timer);
      }
      raf_timer = setTimeout(this.onBrowserVisibilityChange, 500);
    }
  }

  stopObserve(){
    if(this.available === 'onpagehide'){
      window.onpagehide = null;
    } else if(this.available === 'blur'){
      window.removeEventListener('blur', this.onBrowserVisibilityChange, false);
    } else {
      window.cancelAnimationFrame(this.raf_id);
      if(this.available === 'visibilityChange'){
        document.removeEventListener('visibilitychange', this.onBrowserVisibilityChange, false);
      }
    }
  }

  onBrowserVisibilityChange = () =>{
    if(this.availableMethod  === 'visibilityChange'){
      if(document[this.hidden]){
        console.log('hidden')
        this.callbackFunc();
        return true;
      }else{
        console.log('visible')
        return false;
      }
    }
    if(this.availableMethod === 'blur'){
      this.callbackFunc();
    }
  }

}export default CheckBrowserInactive