import React, { Component } from 'react';
import $ from 'jquery';
// components
import ModalTemplate from '../components/modal-template';
import TutorialContents from '../components/tutorial-contents';
// images
import downArrowImg from '../assets/images/down-arrow.png';
// styles
import '../assets/styles/tutorial-modal.css';

class TutorialModal extends Component {
  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  shouldComponentUpdate(nextProps, prevState) {
    if(nextProps.showModal !== this.props.showModal){
      return true
    }
    return false
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    /*
    if(this.props.open){
      this.handleOpenModal();
    }
    */

    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }

    return (
      <div>
        <ModalTemplate showModal={this.props.showModal}
          callBackState={this.handleModal} 
          shouldCloseOnOverlayClick={true}
          isShort={false}
          isSmall={this.props.isSmall}
          revealModal
          >
          <div className="modal-miniTitle" onClick={this.scrollTop}>
            チュートリアル<br />
            Tutorial
          </div>
          <div className={`modal-content scroll-modal`} style={style}>
            <TutorialContents slideNumber={1} />
            <center>
              <img className='tutorialArrow' src={downArrowImg} alt='down'/>
            </center>
            <TutorialContents slideNumber={2} />
            <center>
              <img className='tutorialArrow' src={downArrowImg} alt='down' />
            </center>
            <TutorialContents slideNumber={3} />
            <center>
              <img className='tutorialArrow' src={downArrowImg} alt='down' />
            </center>
            <TutorialContents slideNumber={4} />
            <center style={{marginTop: '-16vw'}}>
              <img className='tutorialArrow' src={downArrowImg} alt='down' />
            </center>
            <TutorialContents slideNumber={5} />
          </div>
        </ModalTemplate>
      </div>
    )
  }
}
export default TutorialModal

