import React, { Component } from 'react';
import { connect } from  'react-redux';
import Countdown from './countdown.js';
import MediaQuery from 'react-responsive';
// styles
import '../assets/styles/teaser.css';
// images
import title from '../assets/images/app-title.png';

class Teaser extends Component{
  render() {
    const MONTH_OFFSET = 1;
    const JST_OFFSET = 9
    const currentDate = new Date();
    const {startDateTime} = this.props.settingData.appSetting;
    const year = (currentDate.getMonth() === 1 && currentDate.getDate() > 3) ? currentDate.getFullYear() + 1 : currentDate.getFullYear();
    const START_DATE = new Date(Date.UTC(year,startDateTime.month - MONTH_OFFSET, startDateTime.day, startDateTime.hour - JST_OFFSET));
    return (
      <div className='Teaser'>
        <div className='cd_backImg'></div>

        <div style={{ marginTop: `${window.innerHeight * .5}px`, transform: 'translateY(-50%)' }}>
          <MediaQuery orientation='portrait'>
            {/* <Countdown date={`${year}-02-04T10:00:00`}/> */}
            <Countdown date={START_DATE.toISOString()}/>
            <div>
              <img src={title}
                className='cd_title'
                alt='title'
                style={{ pointerEvents: 'none' }}/>
            </div>
            <hr style={{ borderTop: '.105vw solid white' }} />
            <div className='cd_textcatchphrase'>2019.2.4 <span className='day'>(MON)</span> 〜 2019.２.11 <span className='day'>(MON)</span><br />札幌エリア限定 / 期間限定</div>
            <hr style={{ borderBottom: '.105vw solid white'}} />
            <div className='cd_textdescription' style={{marginTop: '10vw'}}>特定の場所に行くことで音楽が解放される。<br />
              そんな新しい体験と<span className='name'>SEKAI NO OWARI</span>がコラボレート！<br />
            最新アルバム収録曲を特別先行試聴できます。<br />
            ここでしか味わえない特別な音楽を体験しよう！
            </div>
          </MediaQuery>

          <MediaQuery orientation='landscape'>
            <Countdown date={`${year}-02-04T10:00:00`}/>
            <div>
              <img src={title}
                className='cd_title_ls'
                alt='title'
                style={{ pointerEvents: 'none' }}/>
            </div>
            <hr style={{ width: '50vw', borderTop: '.105vw solid white'}}/>
            <div className='cd_textcatchphrase_ls'>2019.2.4 <span className='day'>(MON)</span> 〜 2019.２.11 <span className='day'>(MON)</span>&nbsp;&nbsp;&nbsp;&nbsp;札幌エリア限定&nbsp;&nbsp;/&nbsp;&nbsp;期間限定</div>
            <hr style={{ width: '50vw', borderBottom: '.105vw solid white'}}/>
            <br />
            <div className='cd_textdescription_ls'>特定の場所に行くことで音楽が解放される。<br />
              そんな新しい体験と<span className='name'> SEKAI NO OWARI </span>がコラボレート！最新アルバム収録曲を特別先行試聴できます。<br />
            ここでしか味わえない特別な音楽を体験しよう！
            </div>
          </MediaQuery>
        </div>
      </div>
    );
}
}

export default connect(state => state)(Teaser);
