import { SET_SOUND_PROGRESS, RESET_SOUND_PROGRESS, SET_SOUND_CONTROLLER_OPENSTATE } from '../types/sounds'

export const setSoundProgress = (soundProgress, volume, frequency) => {
  return {
    type: SET_SOUND_PROGRESS,
    payload: { soundProgress, volume, frequency }
  }
}

export const resetSoundProgress = () => {
  return {
    type: RESET_SOUND_PROGRESS,
    payload: null
  }
}

export const setSoundControllerOpenState = (openState) => {
  return {
    type: SET_SOUND_CONTROLLER_OPENSTATE,
    payload: openState
  }
}
