import axios from 'axios'
import { API_URI, CONSUMER_ID, REQUEST_TOKEN } from '../../config/worldscale-ar-api'

class AxiosUtils {
  constructor() {
    this.TRY_SEND_LIMIT = 2

    this.api = this.createAxios()
    this.promisedApi = this.initForReady()
  }

  createAxios = (accessToken = null, UUID = null) => {
    let init_obj = {baseURL: API_URI}
    if (accessToken) {
      init_obj['headers'] = { 'At': accessToken }
      if (UUID) {
        init_obj['headers']['Cl'] = UUID
      }
    }
    return axios.create(init_obj)
  }

  initForReady = () => {
    let isPromised
    let accessToken = JSON.parse(localStorage.getItem('accessToken'))
    if (accessToken === null) {
      isPromised = this.requestPromisedAccessToken().then(this.initUUID)
    } else {
      this.accessToken = accessToken
      isPromised = this.initUUID(accessToken)
    }
    return isPromised.then((response) => {
      return this.createAxios(this.accessToken, this.uuid)
    })
  }

  requestPromisedAccessToken = () => {
    return this.api.get('/get_actoken', {
      headers: {
        'Consumer-Id': CONSUMER_ID,
        'Request-Token': REQUEST_TOKEN
      }
    }).then((response) => {
      this.accessToken = response.data.results.access_token
      localStorage.setItem('accessToken', JSON.stringify(response.data.results.access_token))
      return response.data.results.access_token
    }).catch((response) => {
      console.error(response)
      throw response
    })
  }

  initUUID = (accessToken) => {
    let uuid = JSON.parse(localStorage.getItem('uuid'))
    if (uuid === null) {
      return this.requestPromisedUUID(accessToken)
    }
    this.uuid = uuid
    return new Promise((resolve, reject) => {
      resolve(uuid)
    })
  }

  requestPromisedUUID = (accessToken) => {
    return this.api.get('/init_thread', {
      headers: {
        'At': accessToken
      }
    }).then( (response) => {
      this.uuid = response.data.results.uuid
      localStorage.setItem('uuid', JSON.stringify(response.data.results.uuid))
      return response.data.results.uuid
    })
  }

  resetAccessToken = () => {
    this.accessToken = null
    localStorage.removeItem('accessToken')
  }

  get = (action, query = {}, count = 0) => {
    if (query === null) query = {}
    let queryString = Object.keys(query).map((key) => {
      return encodeURIComponent(key) + '=' + encodeURIComponent(query[key])
    }).join('&')

    return this.promisedApi.then(
      (api) => {
        return api.get(`/${action}?${queryString}`)
          .then((response) => {
            return response
          }).catch((response) => {
            if (count <= this.TRY_SEND_LIMIT) {
              ++count
              if (response.message.indexOf('code 401') > -1) {
                this.resetAccessToken()
              }
              this.promisedApi = this.initForReady()
              return this.get(action, query, count)
            } else {
              console.error(response)
            }
          })
      }
    )
  }
  post = (action, params = {}, count = 0) => {
    if (params === null) params = {}
    let paramsToSend = new URLSearchParams(params)
    return this.promisedApi.then(
      (api) => {
        return api.post(`/${action}`, paramsToSend)
          .then((response) => {
            return response
          }).catch((response) => {
            if (count <= this.TRY_SEND_LIMIT) {
              ++count
              if (response.message.indexOf('code 401') > -1) {
                this.resetAccessToken()
              }
              this.promisedApi = this.initForReady()
              return this.post(action, params, count)
            } else {
              console.error(response)
            }
          })
      }
    )
  }
}
export default AxiosUtils
