import {SET_PRESENT_OBTAIN, REMOVE_PRESENT_OBTAIN} from '../types/settings'

const LOCAL_STORAGE_PRESENT_OBTAIN = 'presentObtain';
const initState = false
export default (state = initState, action) => {
  switch(action.type) {

    case SET_PRESENT_OBTAIN:
      localStorage.setItem(LOCAL_STORAGE_PRESENT_OBTAIN, JSON.stringify(true))
      return true

    case REMOVE_PRESENT_OBTAIN:
      localStorage.setItem(LOCAL_STORAGE_PRESENT_OBTAIN)
      return false

    default:
      const presentObtain = JSON.parse(localStorage.getItem(LOCAL_STORAGE_PRESENT_OBTAIN))
      return presentObtain ? presentObtain : state
  }
}
