import React, { Component } from 'react'
import ModalTemplate from './modal-template'

class NewSongModal extends Component {
  titleStyle = {
    marginTop: `${(window.innerHeight - 118) * .25}px`,
  }

  contentStyle = {
    marginTop: `${(window.innerHeight - 118) * .5}px`,
  }

  onModalClosed = () => {
    if (this.props.onModalClosed) {
      this.props.onModalClosed()
    }
  }

  formatContent = content => {
    // if(!this.props.soundInfo){
    //   return null
    // }
    return content.split("\n").map( (item,key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )}
    )
  }

  render () {
    const { soundInfo } = this.props;

    if (!soundInfo) return null
    return (
      <ModalTemplate defaultShowModal={true}
                    glow
                    callbackClose={this.onModalClosed} >
          <div className='modal-title'>
            Got a new song!
          </div>
          <div className='soundInfoTitle' style={this.titleStyle} align='center'>
            {this.formatContent(soundInfo.name)}
          </div>
          <div className='soundInfoContent' style={this.contentStyle} align='center'>
            {this.formatContent(soundInfo.comment)}
          </div>
      </ModalTemplate>
    )
  }
}
export default(NewSongModal)
