import React, { Component } from 'react'
import { connect } from 'react-redux'
import {requestSendLog} from '../actions/send-log-api'
import {INIT_APP, AREA_IN, SNS_SHARE, SOUND_ACTION, SOUND_REACTION, COUPON, LOG_ANY} from '../types/logs'
import Button from '@material-ui/core/Button'


class LogChecker extends Component{
  componentDidMount(){
    // if('logType' in this.props.match.params){
    //   this.logType = this.props.match.params.logType;
    // }else{
    //   this.logType = null;
    // }
    // this.switchLogType(this.logType);
    // console.log('send log:'+ this.logType);
  }

  switchLogType(logType){
    let param;
    // console.log('send log:'+ logType);
    switch(logType) {
      default:
        break;

      case INIT_APP:
        this.props.requestSendLog(logType);
        break;

      case AREA_IN:
        this.props.requestSendLog(logType);
        break;

      case SNS_SHARE:
        param = {media:'twitter'};
        this.props.requestSendLog(logType, param);
        break;

      case SOUND_ACTION:
        // param = {action:'start', soudnId:'sound_01'};
        // this.props.requestSendLog(logType, param);
        param = {action:'end', soundId:'sound_01',
                 active_range: '{{x:0.0,y:0.0}}'};
        this.props.requestSendLog(logType, param);
        break;

      case SOUND_REACTION:
        param = {sound_id:'sound_01', sound_progress:'201'};
        this.props.requestSendLog(logType, param);
        break;

      case COUPON:
        param = {coupon_id:'coupon_01', action:'acquire'};
        this.props.requestSendLog(logType, param);
        break;

      case LOG_ANY:
        param = {anything:'you can log anything', something:'something, that you want to log'};
        this.props.requestSendLog(logType, param);
        break;
    }
  }

  render(){
    return(
      <div>
        <br />
        <div>
          <Button variant='contained'  onClick={() => {this.switchLogType(INIT_APP)}}>
          Init App
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(AREA_IN)}}>
          Area In
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(SOUND_ACTION)}}>
          Sound Action Start
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(SOUND_REACTION)}}>
          Sound Reaction
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(SNS_SHARE)}}>
          SNS Share
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(COUPON)}}>
          Coupon Acquisition
          </Button>
          <p></p>
          <Button variant='contained'  onClick={() => {this.switchLogType(LOG_ANY)}}>
          Some random Log
          </Button>
        </div>
      </div>
    )
  }

}
export default connect(state => state, {requestSendLog})(LogChecker)
