// react
import React from 'react'
// redux
import { connect } from 'react-redux'
// container
import BasicAuth from './components/basic-auth';
import EventPeriod from './components/event-period';
import SensorHandleController from './containers/sensor-handle-controller'
import LogHandler from './containers/log-handler';
import AppRoutes from './routes'
// import InitApp from './containers/init-app'
// action
import { initSettings } from './actions/settings'

class Initializer extends React.Component {
  state = {
    geolocation: {
      isDefault: true,
    },
    compassHeading: false,
    deviceOrientation: false,
  }

  componentDidMount() {
    this.props.initSettings()
  }

  onChangeGeolocation = (geolocation) => {
    this.setState({ geolocation })
  }
  onChangeCompassHeading = (compassHeading) => {
    this.setState({ compassHeading })
  }
  onChangeDeviceOrientation = (deviceOrientation) => {
    this.setState({ deviceOrientation })
  }

  render() {
    if (!this.props.settingData) {
      return 'Loading...'
    }

    const { useTeaser, startDateTime, endDateTime } = this.props.settingData.appSetting
    const { geolocation, compassHeading, deviceOrientation } = this.state

    return (
          <SensorHandleController
            callbackGeolocation={this.onChangeGeolocation}
            callbackCompassHeading={this.onChangeCompassHeading}
            callbackDeviceOrientation={this.onChangeDeviceOrientation}
          >
            <LogHandler geolocation={geolocation} >
              <AppRoutes
                geolocation={geolocation}
                compassHeading={compassHeading}
                deviceOrientation={deviceOrientation}
              />
            </LogHandler>
          </SensorHandleController>
    )
  }
}

export default connect(state => state, { initSettings })(Initializer)
