class TimeUtility {
  constructor() {
    this.now = new Date()
  }
  nowInString(separator = false) {
    return this.dateInString(this.now, separator)
  }
  dateInString(date, separator = false) {
    let separator1 = '年'
    let separator2 = '月'
    let separator3 = '日'
    if (separator) {
      separator1 = separator
      separator2 = separator
      separator3 = ''
    }
    return `${date.getFullYear()}${separator1}${date.getMonth()+1}${separator2}${date.getDate()}${separator3}`
  }
  intToDate(year, month, day, hour = false, min = false) {
    // return new Date(`${year}-${month}-${day}`)
    // NOTE: Set date as JST by specifying UTC - 9. Don't use browser local time zone.
    return new Date(Date.UTC(year, --month, day, hour - 9, min))
  }
  isBeforeTheDate(year, month, day, hour = false, min = false) {
    const applied = this.intToDate(year, month, day, hour, min)
    return this.now < applied
  }
  isAfterTheDate(year, month, day, hour = false, min = false) {
    const applied = this.intToDate(year, month, day, hour, min)
    return applied <= this.now
  }
}
export default TimeUtility
