import {
  INIT_APP, ACTIVATE_INIT_APP,
  AREA_IN, ACTIVATE_AREA_IN,
  SNS_SHARE, ACTIVATE_SNS_SHARE,
  SOUND_ACTION, ACTIVATE_SOUND_ACTION,
  SOUND_REACTION, ACTIVATE_SOUND_REACTION,
  COUPON, ACTIVATE_COUPON,
  LOG_ANY, ACTIVATE_LOG_ANY
} from '../types/logs'

export const requestSendLog = (actionName, params = null) => {
  let type = null
  switch(actionName) {
    default:
      type = actionName;
      break;

    case INIT_APP:
      type = ACTIVATE_INIT_APP
      break;

    case AREA_IN:
      type = ACTIVATE_AREA_IN
      break;

    case SNS_SHARE:
      type = ACTIVATE_SNS_SHARE
      break;

    case SOUND_ACTION:
      type = ACTIVATE_SOUND_ACTION
      break;

    case SOUND_REACTION:
      type = ACTIVATE_SOUND_REACTION
      break;

    case COUPON:
      type = ACTIVATE_COUPON
      break;

    case LOG_ANY:
      type = ACTIVATE_LOG_ANY
      break;
  }

  return {
    type,
    payload: params
  }
}
