import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import TimeUtility from '../utils/common/time-utility'
import { sha256 } from 'js-sha256';

class BasicAuth extends Component{
  // Password is 'sekaowarpg'
  //CURRECT_PASS     = 'ddc78654f8c7d06973f59fcd304f1bc6126f6b4864ad923530bd162bd0bf4ff4'
  // Password is 'realitix2019'
  CURRECT_PASS     = 'e18a1b52626aa5eb78b217a781af721b1ac5d6048af178b51decde7864d63535'
  LOCALSTARAGE_KEY = 'BasicAuth'

  state = {
    password : 'Password',
    formHelpText: 'パスワードを入力してください',
    // TODO: When event starts, passed must be set as 'true'
    //passed: true
    passed: false
  }

  componentDidMount(){
    const { startDateTime } = this.props
    if (startDateTime !== false) {
      const timeUtil = new TimeUtility()
      if (timeUtil.isAfterTheDate(startDateTime.year,
                                startDateTime.month,
                                startDateTime.day,
                                startDateTime.hour,
                                startDateTime.min
      ) || localStorage.getItem(this.LOCALSTARAGE_KEY)) {
        this.setState({
          passed: true,
        });
      }
    }
  }

  handleChange = (event) => {
      this.setState({ password: event.target.value });
  };

  applyPassword = () => {
    if(sha256(this.state.password) === this.CURRECT_PASS){
      // NOTE: 応急処置。this.props.startDateTime === false の時はlocalStorageに保存せず毎回認証させる事とする対応。ほんとは別フラグで判断すべきだけど。
      if (this.props.startDateTime !== false) {
        localStorage.setItem(this.LOCALSTARAGE_KEY, true);
      }
      this.setState({passed: true})
    } else {
      this.setState({formHelpText: 'パスワードが違います'});
    }
  }

  render(){
    if (!this.state.passed) {
      return(
        <form>
          {/* username input is not needed, but only for removing chrome's warning */}
          <input type="text" name="username" style={{display:"none"}} autoComplete="username"/>
          <TextField
            id="standard-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
            margin="normal"
            onChange={this.handleChange}
            helperText={this.state.formHelpText}
          />
          <Button variant='contained'  onClick={this.applyPassword}>
            Login
          </Button>
        </form>
      )
    }
    return this.props.children
  }
}

BasicAuth.propTypes = {
  startDateTime: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]).isRequired,
}
export default BasicAuth
