import _ from 'lodash'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import AreainModal from './areain-modal'
import NewSongModal from './new-song-modal'
import CouponGetModal from './coupon-get-modal'
import CompleteSongModal from './complete-song-modal'
import PrizeInfoModal from './prize-info-modal'
import SoundController from './sound-controller'
import { setSoundComplete } from '../actions/settings'
//actions
import { setHamburgerOpenState } from '../actions/hamburger';
import { setSoundControllerOpenState } from '../actions/sounds';

const STEP_DEFAULT        = 0
const STEP_FIRST_MODAL    = 1
const STEP_COUPON_MODAL   = 2
const STEP_NEW_SONG_MODAL = 3
const STEP_COMPLETE_MODAL = 4
const STEP_PRIZE_MODAL    = 5

class CheckinModals extends Component {

  state = {
    soundList: {},
    modalArea: false,        // Area has to be fixed while modal process is going on
    processTrigger: false,
    forceCompleteTrigger: false,
    step: STEP_DEFAULT,
    couponId: null,
    // NOTE: checkedInSoundId and selectedSoundId have to be set at the different timing each, so you can't mix them.
    checkedInSoundId: null,  // It's used to control modal contents
    // selectedSoundId: null,   // It's used to control sound controller
    blinkList: false,
    isVertical: true
  }

  componentDidMount() {
    // NOTE: This is testing.
    //setTimeout(() => {this.setState({processTrigger: true, modalArea:'place1'})}, 500)
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { settingData, currentArea, soundComplete } = nextProps;
    const { soundList } = settingData
    const ret = {}
    if (!_.isEqual(soundList, prevState.soundList)) {
      ret.modalArea = currentArea
      ret.soundList = soundList
      if (CheckinModals.checkActivateTrigger(soundList, prevState.soundList)) {
        ret.processTrigger = true
      }
    }

    if (!soundComplete && !ret.processTrigger && (prevState.step === STEP_DEFAULT) && CheckinModals.checkForceComplete(soundList)) {
      ret.forceCompleteTrigger = true
    }

    const isVertical = CheckinModals.isVertical(nextProps.deviceOrientation)
    if ( isVertical !== prevState.isVertical ) {
      ret.isVertical = isVertical
    }

    return ret
  }
  static isVertical = (deviceOrientation) => {
    return (deviceOrientation === 0) || (deviceOrientation === 180)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { modalArea } = this.state
    const { settingData } = this.props
    const { appSetting } = settingData
    const { areas } = appSetting

    if (this.state.processTrigger && modalArea && (modalArea !== 'none')) {
      this.processStart(areas[modalArea]) // begin all the process here
    }
    if (this.state.forceCompleteTrigger) {
      this.setState( {forceCompleteTrigger: false} )
      this.onComplete()
    }

    if (!this.props.soundControllerOpenState && this.state.blinkList) {
      this.setState({blinkList: false})
    }
  }

  static checkActivateTrigger = (nextSoundList, prevSoundList) => {
    if (Object.keys(prevSoundList).length === 0) {
      return false
    }
    for ( let key in nextSoundList) {
      if (nextSoundList[key].isBonus && nextSoundList[key].unlocked) {
        // Take it as false, if bonus sound is unlocked
        return false
      }
    }
    return true
  }
  static checkForceComplete = (soundList) => {
    if (Object.keys(soundList).length === 0) {
      return false
    }
    let allBonusUnlocked = true
    let allNoBonusUnlocked = true
    for ( let key in soundList) {
      if (soundList[key].isBonus) {
        if (!soundList[key].unlocked) {
          allBonusUnlocked = false
        }
      } else {
        if (!soundList[key].unlocked) {
          allNoBonusUnlocked = false
        }
      }
    }
    if (!allBonusUnlocked && allNoBonusUnlocked) {
      return true
    }
    return false
  }


  // Process flow ----------------------
  processStart = (area) => {
    this.setState( {processTrigger: false} )
    if (window.navigator.vibrate) {
      window.navigator.vibrate([100,30,100,30,100,30])
    }
    if (this.props.hamburgerOpenState) {
      this.props.setHamburgerOpenState(false)
    }
    if (this.props.soundControllerOpenState) {
      this.changePlayerOpenness(false)
    }
    let couponId = null
    if (area.couponId) {
      couponId = area.couponId
    }
    this.setState( {couponId} )
    this.setState( {checkedInSoundId: area.soundId} )
    this.openFirstModal()
  }
  openFirstModal = () => {
    this.setStep(STEP_FIRST_MODAL)
  }
  onCheckin = () => {
    this.setStep(STEP_NEW_SONG_MODAL)
  }
  onNewSongModalClosed = () => {
    let unlockCount = 0
    let requiredCount = 0
    _.map(this.state.soundList, soundInfo => {
      if (soundInfo.isBonus) {
        return false
      }
      requiredCount++
      if (soundInfo.unlocked) {
        unlockCount++
      }
    })

    if (unlockCount === requiredCount) {
      // The last song
      this.onComplete()
    } else {
      // if (unlockCount === 1) {
      //   // The first song
      //   this.onSoundSelected(this.state.checkedInSoundId)
      // }
      this.stepBeforeFinalization()
    }
  }
  onComplete = () => {
    this.props.setSoundComplete()
    this.setStep(STEP_COMPLETE_MODAL)
  }
  onCompleteModalClosed = () => {
    this.setStep(STEP_PRIZE_MODAL)
  }
  onPrizeModalClosed = () => {
    this.stepBeforeFinalization()
  }
  stepBeforeFinalization = () => {
    if (this.state.couponId) {
      this.setStep(STEP_COUPON_MODAL)
      return true
    }
    this.stepFinalization()
  }
  onCouponModalClosed = () => {
    this.stepFinalization()
  }
  stepFinalization = () => {
    this.changePlayerOpenness(true)
    const blinkList = [this.state.checkedInSoundId]
    if (this.props.soundComplete) {
      blinkList.push("YokohamaBlues")
    }
    this.setState({blinkList})
    this.resetStep()
  }

  // Step controller -------------------
  resetStep = () => {
    this.setStep(STEP_DEFAULT)
  }
  setStep = (step) => {
    this.setState({ step })
  }

  // State controller ------------------
  changePlayerOpenness = (openState) => {
    this.props.setSoundControllerOpenState(openState)
  }
  // onSoundSelected = (soundId) => {
  //   this.setState( {selectedSoundId: soundId} )
  // }
  // onChildSoundSelected = (soundInfo) => {
  //   this.onSoundSelected(soundInfo.key)
  // }

  renderModal = () => {
    const areaInfo = this.props.settingData.appSetting.areas[this.state.modalArea];
    const {step, checkedInSoundId, soundList, couponId } = this.state;
    const {couponList} = this.props.settingData;

    switch(step) {
      case STEP_FIRST_MODAL:
        return <AreainModal areaInfo={areaInfo} callback={this.onCheckin} />
      case STEP_COUPON_MODAL:
        return <CouponGetModal couponInfo={couponList[couponId]} onModalClosed={this.onCouponModalClosed}/>
      case STEP_NEW_SONG_MODAL:
        return <NewSongModal soundInfo={soundList[checkedInSoundId]} onModalClosed={this.onNewSongModalClosed}/>
      case STEP_COMPLETE_MODAL:
        return <CompleteSongModal soundInfo={soundList.YokohamaBlues} onModalClosed={this.onCompleteModalClosed}/>
      case STEP_PRIZE_MODAL:
        return <PrizeInfoModal onModalClosed={this.onPrizeModalClosed}/>
      default:
        return null
    }
  }

  render () {
    const { soundList } = this.state
    return (
      <div>
        {this.renderModal()}
        <SoundController soundList={soundList}
                         blinkList={this.state.blinkList}
        />
      </div>
    )
    // soundInfo={soundList[selectedSoundId]}
    // callbackSoundInfo={this.onChildSoundSelected}
  }
}

export default connect(state => state, { setSoundComplete, setHamburgerOpenState, setSoundControllerOpenState })(CheckinModals)
